import {
  IMPORT_CAPTURE_MODEL,
  IMPORT_RESOURCE_CHOICE,
  IMPORT_RESOURCE_FIELD,
  IMPORT_RESOURCE_TREE,
  IMPORT_UNKNOWN_RESOURCE,
  RESOURCE_NAV_BACK,
  RESOURCE_NAV_FORWARD,
  RESOURCE_NAV_RESET
} from '../actions/resources';

import update from 'immutability-helper';

const defaultState = {
  currentPaths: {},
  trees: {},
  list: {},
};

const captureModelDefaults = {
  fields: {},
};

export default function resourceReducer(state = defaultState, action) {
  if (action.error) {
    switch (action.type) {
      case IMPORT_RESOURCE_TREE:
      case IMPORT_UNKNOWN_RESOURCE:
      case IMPORT_RESOURCE_FIELD:
        console.error(action.payload);
        return state;
    }

    return state;
  }

  switch (action.type) {

    case RESOURCE_NAV_FORWARD:
      if (!state.currentPaths[action.payload.tree]) {
        // @todo Log error.
        return state;
      }
      return update(state, {
        currentPaths: { [action.payload.tree]: { $push: [action.payload.path] } },
      });

    case RESOURCE_NAV_BACK:
      if (!state.currentPaths[action.payload.tree]) {
        // @todo Log error.
        return state;
      }
      return update(state, {
        currentPaths: { [action.payload.tree]: { $apply: (path) => path.length > 0 ? path.slice(0, -1) : [] } },
      });

    case RESOURCE_NAV_RESET:
      return update(state, {
        currentPaths: { [action.payload.tree]: { $set: [] } },
      });

    case IMPORT_RESOURCE_TREE:
      return update(state, {
        currentPaths: {
          [action.payload.id]: { $set: [] },
        },
        trees: {
          [action.payload.id]: { $set: action.payload.tree },
        },
      });

    case IMPORT_RESOURCE_CHOICE:
      return state; // This all exists in the tree for now.

    case IMPORT_CAPTURE_MODEL:
      return update(state, {
        list: {
          [action.payload.id]: { $set: { ...captureModelDefaults, ...action.payload.resource } },
        },
      });

    case IMPORT_RESOURCE_FIELD:
      return update(state, {
        list: {
          [action.payload.id]: {
            fields: {
              [action.payload.resource.id]: { $set: action.payload.resource },
            },
          },
        },
      });
      return state;
  }

  return state;
}



