import React, { PureComponent } from 'react';
import { connector } from 'digirati-annotation-redux';
import { getAllAnnotationsFromCollection } from 'digirati-annotation-redux/es/lib/elucidate/index';
import { asyncForEach } from './core.utils';
import 'whatwg-fetch';

class Core extends PureComponent {

  componentDidMount() {
    const {
      setServer,
      canvas: canvasId,
      manifest,
      createCollection,
      addElucidateAnnotation,
      setResource,
      elucidateServer,
      addManifest,
      selectManifest,
      selectCanvas,
    } = this.props;

    const subject = canvasId ? canvasId : manifest;

    if (!elucidateServer) {
      return;
    }
    if (canvasId) {
      fetch(manifest)
        .then(response => response.json())
        .then(response => addManifest(manifest, response))
        .then(response => selectManifest(manifest))
        .then(response => canvasId ? selectCanvas(canvasId) : null);
    }
    // Set the server.
    setServer(elucidateServer, subject);
    // Set the resource.
    if (subject) {
      setResource(subject);
    }

    // Set collection.
    createCollection(subject, subject)
      .then(c => getAllAnnotationsFromCollection(c))
      .then(iterable =>
        asyncForEach(iterable, i =>
          addElucidateAnnotation(i)
        ),
      );
  }

  render() {
    return (
      <div style={{ display: 'none'}}>
        Annotation studio is running on this page.
        <pre>
            { /*JSON.stringify(this.props.state, null, 2)*/ }
        </pre>
      </div>
    );
  }
}

export default connector(Core);



