export function currentManifest(state) {
  return manifestById(state, state.manifest.current);
}

export function getImageServiceFromCanvas(canvas) {
  if (
    canvas.images &&
    canvas.images[0] &&
    canvas.images[0].resource &&
    canvas.images[0].resource.service &&
    canvas.images[0].resource.service['@id']
  ) {
    return `${canvas.images[0].resource.service['@id']}/info.json`;
  }

  if (
    canvas.thumbnail &&
    canvas.thumbnail.service &&
    canvas.thumbnail.service['@id']
  ) {
    return `${canvas.thumbnail.service['@id']}/info.json`;
  }
  if (
    canvas.thumbnail &&
    canvas.thumbnail['@id']
  ) {
    return canvas.thumbnail['@id'];
  }

  return null;
}

export function currentImageService(state) {
  const canvas = currentCanvas(state);
  if (!canvas) {
    return null;
  }

  return getImageServiceFromCanvas(canvas);
}

export function currentCanvas(state) {
  const manifest = currentManifest(state);
  const canvasId = state.manifest.currentCanvas;
  if (!(
      manifest &&
      canvasId &&
      manifest.sequences &&
      manifest.sequences[0] &&
      manifest.sequences[0].canvases
    )) {
    return null;
  }
  const canvases = manifest.sequences[0].canvases;
  for (let canvas of canvases) {
    if (canvas['@id'] === canvasId) {
      return canvas;
    }
  }
}

export function manifestById(state, id) {
  if (!id) {
    return null;
  }
  if (!state.manifest.list[id]) {
    return null;
  }
  return state.manifest.list[id];
}

export function allManifests(state) {
  const manifestKeys = Object.keys(state.manifest.list);
  if (!manifestKeys || !manifestKeys.length) {
    return [];
  }
  return manifestKeys.map(key => state.manifest.list[key]);
}



