import React from 'react';
import BEM from 'digirati-bem-js';
import './resourceNavigation.scss';

const $resourceNavigation = BEM.block('resource-navigation');

/**
 * Loops through the available capture models and displays information about them.
 * Using the passed in createDraft the UI should attach a click handler.
 */
export default function resourceNavigation({resourceTemplates, createDraft, moveForward, moveBack, showBackButton}) {

  const handleItemClick = (item) => () => {
    moveForward(item.id);
    if (item.type === 'captureModel') {
      createDraft(item);
    }
  };

  return (
    <div className={$resourceNavigation}>
      <h2 className={$resourceNavigation.element('heading')}>What do you want to tag</h2>
      {
        Object.values(resourceTemplates).map(item => (
          <div key={item.id}
               className={$resourceNavigation.element('metadata')}
               onClick={handleItemClick(item)}>
            <div className={$resourceNavigation.element('title')}>{item.title}</div>
            <div className={$resourceNavigation.element('description')}>{item.description}</div>
          </div>
        ))
      }
      {showBackButton ? (
        <div onClick={moveBack} className={$resourceNavigation.element('back')}>
          back
        </div>
      ) : null}
    </div>
  );
}



