import React, { PureComponent } from 'react';
import DatePicker from './DatePicker';
import DropDown from './DropDown';
import GoogleMapSearch from './GoogleMapSearch';
import TextArea from './TextArea';
import TextBox from './TextBox';
import NullableCaptureModel from './NullableCaptureModel';
import VALIDATION_MAP from '../domain/validation';
import EmbeddedResourceEditor from './EmbeddedResourceEditor';

export default class FormField extends PureComponent {

  update(validate, field) {
    const { updateField } = this.props;
    return (value) => {
      // if (validate) {
      //   const validation = validate(value);
      //   if (!validation.isValid) {
      //     this.setState({error: validation.message});
      //   } else {
      //     if (this.state.error) {
      //       this.setState({error: null});
      //     }
      //   }
      // }
      updateField(field, value);
    };
  }

  render() {
    const { field, draftId, ...allProps } = this.props;
    const validation = ( field.metaData && field.metaData.conformsTo ) ? VALIDATION_MAP[field.metaData.conformsTo.id] : null;
    const update = this.update(validation, field).bind(this);
    const props = {
      key: `${field.id}-${draftId}`,
      update,
      ...allProps,
      ...field
    };
    switch (field.inputType) {
      case 'madoc:dropdown':
        if (field.annotationMetaData.options) {
          return <DropDown key={props.id} autocomplete={field.annotationMetaData.autocomplete || null}
                           options={field.annotationMetaData.options || []} {...props}/>;
        }
        return <TextBox key={props.id} {...props}/>;
      case 'madoc:textarea':
        return <TextArea key={props.id} {...props}/>;
      case 'madoc:datepicker':
        return <DatePicker key={props.id} {...props}/>;
      case 'madoc:map':
      case 'madoc:mappicker':
        return <GoogleMapSearch key={props.id} {...props}/>;
      case 'madoc:nullableCaptureModel':
        return <NullableCaptureModel key={props.id} {...props}/>;
      case 'madoc:resource':
        return <EmbeddedResourceEditor key={props.id} {...props}/>;
      case 'madoc:textbox':
      default:
        return <TextBox key={props.id} {...props}/>;
    }
  }

}



