import React from 'react';
import {render} from 'react-dom';
import {createStore, reducers} from 'digirati-annotation-redux';
import {unknownPropertyWarning} from './core.utils';
import CoreProvider from './core.provider';

export default function CorePlugin($el, {
  manifest,
  canvas,
  elucidateServer,
  ...unknownProps
}) {
  unknownPropertyWarning(unknownProps);

  const store = createStore(reducers);
  render(
    <CoreProvider
      store={store}
      manifest={manifest}
      elucidateServer={elucidateServer}
      canvas={canvas}
    />
    , $el);

  return Promise.resolve(store);
}



