import React, { Component } from 'react';
import BEM from 'digirati-bem-js';
import './TextBox.scss';

const style = BEM.block('input-text-box');

class TextBox extends Component {

  shouldComponentUpdate(newProps) {
    return newProps.value !== this.props.value || newProps.preview !== this.props.preview;
  }

  render() {
    const { id, value, metaData, preview, update, displayFieldSelector, elementEvents } = this.props;

    const events = {
      ...elementEvents,
      onFocus: e => elementEvents.onFocus ? elementEvents.onFocus(id, e) : null,
      onBlur: e => elementEvents.onBlur ? elementEvents.onBlur(id, e) : null,
    };


    return (
      <div className={style}>
        <label className={style.element('label')} htmlFor={id}>
          {metaData.title} {' '}
          { (metaData.selector && metaData.selectorValue) ?
            (<a onClick={(e) => {
              e.preventDefault();
              displayFieldSelector(id);
            }} href="#">edit</a>)
          : null }
        </label>
        <div className={style.element('description')}>
          {metaData.description}
        </div>
        { preview ? (
          <div className={style.element('preview')}>{value}</div>
        ) : (
          <input className={style.element('input')}
                 id={id}
                 type="text"
                 value={value || ""}
                 onChange={e => update(e.currentTarget.value)}
                 {...events}/>
        ) }
      </div>
    );
  }

}

export default TextBox;



