import React, { Component } from 'react';
import EmbeddedResourceEditor from './EmbeddedResourceEditor';
import BEM from 'digirati-bem-js';

const style = BEM.block('nullable-capture-model');
export default class NullableCaptureModel extends Component {

  state = { isChecked: false, isSaved: false, annotation: null };

  onReceiveEmbeddedItem = item => item.then(item => {
    const id = item.payload.annotation.id;
    this.props.update(id);
    this.setState({ isSaved: true, annotation: item.payload.annotation });
  });

  handleChange = () => {
    const { captureModel } = this.props;
    const isChecked = !this.state.isChecked;
    if (!this.props.value) {
      this.props.update(isChecked ? (captureModel) : null);
    }
    this.setState({ isChecked });
  };

  render() {
    const { isChecked, isSaved, annotation } = this.state;
    const {id, metaData, annotationMetaData, displayFieldSelector, ...props} = this.props;
    const checkbox = annotationMetaData.options.find(e => e['o:label'] === 'madoc:checkbox');
    const captureModel = annotationMetaData.options.find(e => e['o:label'] === 'madoc:capturemodel');
    if (!checkbox || !captureModel) {
      console.warn('NullableCaptureModel component failed to mount.', this.props);
      return;
    }
    return (
      <div>
        <input id={id} type="checkbox" value={this.state.isChecked} onChange={this.handleChange}/>
        <label className={style.element('label')} htmlFor={id}>
          {metaData.title} {' '}
          { (metaData.selector && metaData.selectorValue) ?
            (<a onClick={(e) => {
              e.preventDefault();
              displayFieldSelector(id);
            }} href="#">edit</a>)
            : null }
        </label>
        {isChecked ? (
          <div>
            { isSaved ? (
              <div>
                { annotation.label } ({ annotation.id })
              </div>
              ) : (
              <EmbeddedResourceEditor captureModel={captureModel['@id']} postSave={this.onReceiveEmbeddedItem} {...props}/>
            ) }
          </div>
        ) : null}
      </div>
    );
  }
}



