import React, { Component, PropTypes } from 'react';
import Form from '../inputs/Form';
import BEM from 'digirati-bem-js';
import isEqual from 'lodash.isequal';

const baseStyle = BEM.block('full-form');

class ConfirmationForm extends Component {

  // propTypes = {
  //   draft: PropTypes.shape({
  //     id: PropTypes.string.isRequired,
  //     isPreviewing: PropTypes.bool.isRequired,
  //     input: PropTypes.object.isRequired,
  //   }),
  //   bem: PropTypes.object,
  //   nextLabel: PropTypes.string,
  //   editLabel: PropTypes.string,
  //   saveLabel: PropTypes.string,
  //   updateField: PropTypes.func.isRequired,
  //   onEdit: PropTypes.func,
  //   onSave: PropTypes.func,
  //   previewDraft: PropTypes.func.isRequired,
  // };

  shouldComponentUpdate(nextProps) {
    return !isEqual(this.props, nextProps);
  }

  render() {
    const {
      // Variables.
      draft,
      fields,
      bem,
      nextLabel,
      editLabel,
      saveLabel,
      children,
      // Actions.
      updateField,
      onEdit,
      onSave,
      previewDraft,
      ...props,
    } = this.props;

    const style = bem ? bem : baseStyle;

    return (
      <div className={style}>
        { children ? children : (
          <Form
            draft={draft}
            fields={fields}
            updateField={(input, value) => updateField(draft.id, { [input.id]: value }) }
            { ...props }
          />
        )}
        { draft.isPreviewing ? (
          <div className={style.element('actions')}>
            { onEdit ? (
              <button className={style.element('action-btn').modifiers({ first: true, last: !!onSave, })}
                      onClick={() => onEdit(draft)}>
                { editLabel ? editLabel : 'edit' }
              </button>
            ) : null }
            { onSave ? (
              <button className={style.element('action-btn').modifiers({ first: !!onEdit, last: true })}
                      onClick={() => onSave(draft) }>
                { saveLabel ? saveLabel : 'save' }
              </button>
            ) : null }
          </div>
        ) : (
          <button className={style.element('action-btn').modifiers({ first: true, last: true })}
                  onClick={() => previewDraft(draft.id, draft)}>
            { nextLabel ? nextLabel : 'next' }
          </button>
        )}
      </div>
    );
  }
}

export default ConfirmationForm;



