import { ID, OMEKA_LABEL } from './vocab';

// HELPER
export function castBool(str) {
  if (str === true) return true;
  if (str === false) return false;
  return str.toLowerCase() === 'true';
}

export function parseSelectorTarget(toParse, scale = 1) {
  if (!toParse) {
    return toParse;
  }
  const W3C_SELECTOR = /[#&?](xywh=)?(pixel:|percent:)?([0-9]+(\.[0-9]+)?),([0-9]+(\.[0-9]+)?),([0-9]+(\.[0-9]+)?),([0-9]+(\.[0-9]+)?)/;
  const match = W3C_SELECTOR.exec(toParse);

  if (match) {
    return {
      unit: (match[2] === 'percent:' ? 'percent' : 'pixel'),
      scale,
      expanded: true,
      x: parseFloat(match[3]) * scale,
      y: parseFloat(match[5]) * scale,
      width: parseFloat(match[7]) * scale,
      height: parseFloat(match[9]) * scale,
      toString() {
        // @todo maybe something else?
        return toParse.split('#')[0];
      },
    };
  }
  return toParse;
}

export function castId(value, defaultValue) {
  return (value[ID] ? value[ID] : value) || defaultValue;
}

export function get(value) {
  return (prop, defaultValue) => value ? value[prop] : defaultValue;
}

export function getMotivationInstance(str) {
  const motive = str.toLowerCase();
  // Remove the open annotation prefix.
  if (motive.indexOf('oa:') === 0) {
    return getMotivationInstance(motive.substr(3));
  }
  if (motive.indexOf('o:') === 0) {
    return getMotivationInstance(motive.substr(2));
  }
  if ([
      'assessing',
      'bookmarking',
      'classifying',
      'commenting',
      'describing',
      'editing',
      'highlighting',
      'identifying',
      'linking',
      'moderating',
      'questioning',
      'replying',
      'tagging',
    ].indexOf(motive) !== -1) {
    return motive;
  }

  // This is the default to fallback to.
  return 'tagging';
}

export function motivationFromForm(motivatedBy) {
  if (!motivatedBy) {
    return null;
  }
  if (!motivatedBy.id) {
    return null;
  }
  return {
    id: motivatedBy.id,
    label: motivatedBy.label ? motivatedBy.label : motivatedBy.id,
    instance: getMotivationInstance(motivatedBy.id),
  }
}

export function labelId(res, defaultValue) {
  if (!res) {
    return defaultValue ? defaultValue : {
      id: null,
      label: 'unknown',
    }
  }
  if (Object.prototype.toString.call(res) === '[object Array]') {
    return res.map(labelId);
  }

  return {
    id: res[ID],
    label: res[OMEKA_LABEL],
  }
}

export function iteratorGenerator(generator) {
  const instance = generator();
  return (...props) => {
    return instance.next(props).value;
  }
}



