import React from 'react';
import { render } from 'react-dom';
import { utils } from 'digirati-annotation-plugin-core';
import ResourceEditorProvider from './resourceEditor.provider';
import { importResourceTemplate } from 'digirati-annotation-bridge';
import { getResourceById } from 'digirati-annotation-redux/es/query/resourceQuery';

export default function ResourceEditorPlugin($el, {
  canvas,
  manifest,
  serializationStrategy,
  resourceTemplates,
  target = 'canvas',
  ...unknownProps
}, store) {
  utils.unknownPropertyWarning(unknownProps);

  if (resourceTemplates) {
    const { dispatch } = store;

    const importCaptureModel = (resourceTemplates) => {
      let resourceTemplate = getResourceById(store.getState(), resourceTemplates);
      if (resourceTemplate) {
        return resourceTemplate;
      }

      return fetch(`${resourceTemplates}`)
      .then(r => r.json())
      .then(resourceTemplate => {
        dispatch(importResourceTemplate(resourceTemplate, target));
        return resourceTemplate;
      });
    };

    return importCaptureModel(resourceTemplates)
    .then(resourceTemplate => {
      render((
        <ResourceEditorProvider
          store={store}
          target={target}
          tree={resourceTemplate['@id']}
          serializationStrategy={serializationStrategy}
          manifest={manifest}
          importCaptureModel={importCaptureModel}
          canvas={canvas}/>
      ), $el);
      return store;
    });
  }
}



