import React, { PureComponent, PropTypes } from 'react';
import BEM from 'digirati-bem-js';
import './TextBox.scss';

const style = BEM.block('input-text-box');

class TextArea extends PureComponent {

  // propTypes = {
  //   id: PropTypes.string.isRequired,
  //   value: PropTypes.string.isRequired,
  //   update: PropTypes.func.isRequired,
  //   metaData: PropTypes.shape({
  //     title: PropTypes.string.isRequired,
  //     description: PropTypes.string
  //   }).isRequired,
  //   preview: PropTypes.bool,
  // };

  render() {
    const { id, value, metaData, preview, displayFieldSelector } = this.props;
    return (
      <div className={style}>
        <label className={style.element('label')} htmlFor={id}>
          {metaData.title} {' '}
          { (metaData.selector && metaData.selectorValue) ?
            (<a onClick={(e) => {
              e.preventDefault();
              displayFieldSelector(id);
            }} href="#">edit</a>)
            : null }
        </label>
        <div className={style.element('description')}>{metaData.description}</div>
        { preview ? (
          <div className={style.element('preview')}>{value}</div>
        ) : (
          <textarea className={style.element('input').modifier('textarea')}
                    id={id}
                    value={value || ''}
                    onChange={e => this.props.update(e.currentTarget.value)}/>
        ) }
      </div>
    );
  }

}

export default TextArea;



