export function getCurrentDraftId(state, scope) {
  return state.drafts.currentDrafts[scope];
}

export function getCurrentDrafts(state) {
  return Object.values(state.drafts.currentDrafts);
}

export function getDraftById(state, id) {
  return state.drafts.list[id];
}

export function getCurrentDraft(state, scope) {
  return getDraftById(state, getCurrentDraftId(state, scope));
}

export function getAllDraftsWithSelectors(state) {
  return getDraftsWithSelectors(state.drafts.list);
}

export function getDraftsWithSelectors(drafts) {
  return Object.keys(drafts)
    .filter(k => drafts[k] && drafts[k].selector)
    .map(k => drafts[k]);
}

export function currentDraftEditableRegions(state) {
  const currentDrafts = getCurrentDrafts(state);
  if (!currentDrafts) {
    return [];
  }
  return editableRegionsFromDraftList(state.drafts.list, currentDrafts);
}

// @todo to revise.
export function mapDraftSelectorToRegion(draft, currentDraftKeys, onClickRegion, currentSelectorExists) {
  const selector = draft.selector;
  const keysAsString = currentDraftKeys.map(e => `${e}`);
  return {
    id: draft.id,
    onClick: () => onClickRegion(draft.id),
    selected: keysAsString.indexOf(`${draft.id}`) !== -1,
    editing: keysAsString.indexOf(`${draft.id}`) !== -1 && currentSelectorExists,
    immutable: false,
    ...selector
  }
}

export function editableRegionsFromDraftList(drafts,
  currentDraftKeys = [],
  onClickRegion = () => null,
  currentSelectorExists = true,) {
  return getDraftsWithSelectors(drafts).map(draft => mapDraftSelectorToRegion(
    draft,
    currentDraftKeys,
    onClickRegion,
    currentSelectorExists,
  ));
}



