import type Motivation from './Annotation';
import Annotation from './Annotation';
import AnnotationBodyList from './AnnotationBodyList';
import AnnotationSelector from './AnnotationSelector';
import AnnotationBody from './AnnotationBody';
import { getForm } from '../../query/resourceQuery';

// Draft value is {id: 'http://..'}
export const FIELD_TYPES_THAT_ARE_ENTITIES = ['madoc:nullableCaptureModel', 'madoc:fallbackCaptureModel'];
// Draft value is {label: 'human readable'}
export const FIELD_TYPES_THAT_ARE_HUMAN_READABLE = ['madoc:nullableCaptureModel', 'madoc:fallbackCaptureModel'];

export function createCombinedAnnotationBody(captureModel, draft): Array<AnnotationBody> {
  return []; // @todo
}

/**
 * @deprecated for reference only
 */
export function dataSetToSingleAnnotation(template, dataset) {
  const { metaData: { title, description } } = template;

  dataset.context = dataset.context ? dataset.context : {};
  if (title) {
    dataset.context.Title = 'http://dublincore.org/documents/dcmi-terms/#terms-title';
    dataset.Title = title;
  }
  if (description) {
    dataset.context.Description = 'http://dublincore.org/documents/dcmi-terms/#terms-description';
    dataset.Description = description;
  }

  const body = {
    type: ['TextualBody', 'Dataset'],
    value: JSON.stringify(dataset),
    format: 'text/plain',
  };

  return { body, label: title };
}

/**
 * @deprecated for reference only
 */
export function getIndividualW3CAnnotationBodies(captureModel, draft) {
  const fields = Object.values(getForm(captureModel).fields);

  const selectors = Object.keys(draft.selectors || {})
  .map(key => ({ id: key, value: draft.selectors[key] }))
  .map(selector => {
    const capModel = fields.find(model => model.id === selector.id);
    return dataSetToSingleAnnotation(
      capModel,
      mapDraftToDataSet(fields, { [selector.id]: draft.input[selector.id] }),
    );
  });
  const keyedSelectors = {};
  for (let selector of selectors) {
    keyedSelectors[selector.id] = selector.annotation;
  }

  return keyedSelectors;
}

export function mapFieldsToDataSet(fields, data) {
  const contexts = {};
  const body = {};

  fields.filter(e => e).forEach(model => {
    if (model.metaData.conformsTo && data[model.id]) {
      if (model.metaData.conformsTo.label !== model.metaData.conformsTo.id) {
        contexts[model.metaData.conformsTo.label] = model.metaData.conformsTo.id;
      }
      body[model.metaData.conformsTo.label] = data[model.id];
    }
  });

  return {
    '@context': contexts,
    ...body
  };
}

function getModelFields(ids, fields) {
  if (Array.isArray(fields)) {
    return ids.map(fieldId => fields.find(field => field.id === fieldId));
  }
  return ids.map(fieldId => fields[fieldId]);
}

export function mapDraftToDataSet(captureModel, draft) {
  const fieldIds = Object.keys(draft.input);
  const fields = getModelFields(fieldIds, captureModel.fields);
  return mapFieldsToDataSet(fields, draft.input);
}


export function createExternalResourceAnnotationBody(captureModel, draft): Array<AnnotationBody> {
  const fieldIds = Object.keys(draft.input);
  const fields = getModelFields(fieldIds, captureModel.fields);
  // const fields = fieldIds.map(fieldId => captureModel.fields.find(field => field.id === fieldId));
  return fields.filter(e => e).reduce((state, field) => {
    if (FIELD_TYPES_THAT_ARE_ENTITIES.indexOf(field.inputType) !== -1) {
      const draftInput = draft.input[field.id];
      if (!draftInput) {
        return state;
      }
      if (draftInput.url) {
        state.push(
          new AnnotationBody(
            draftInput.url,
            null,
            'application/json',
            'SpecificResource',
          ),
        );
      }
    }
    return state;
  }, []);
}

export function createHumanReadableAnnotationBody(captureModel, draft): Array<AnnotationBody> {
  const fieldIds = Object.keys(draft.input);
  const fields = getModelFields(fieldIds, captureModel.fields);
  // const fields = fieldIds.map(fieldId => captureModel.fields.find(field => field.id === fieldId));

  return fields.filter(e => e).reduce((state, field) => {
    if (FIELD_TYPES_THAT_ARE_HUMAN_READABLE.indexOf(field.inputType) !== -1) {
      const draftInput = draft.input[field.id];
      if (!draftInput) {
        return state;
      }
      if (draftInput.label) {
        state.push(
          new AnnotationBody(
            null,
            draftInput.label,
            'text/plain',
            'Text',
          ),
        );
      }
    }
    return state;
  }, []);
}

export function createSerializedAnnotationBody(captureModel, draft): ?AnnotationBody {
  const dataSet = mapDraftToDataSet(captureModel, draft);

  if (dataSet) {
    return new AnnotationBody(
      null,
      JSON.stringify(dataSet),
      'text/plain',
      'Dataset',
    )
  }
  return null;
}

export function createMultipleAnnotations(target, captureModel, draft): ?Annotation {
  const fieldKeys = Object.keys(draft.input);

  const fields = fieldKeys.map(key => draft.input[key] ? ({
    id: key,
    input: draft.input[key],
    selector: draft.selectors[key]
  }) : null).filter(e => e);

  return fields.map(field => {
    const captureModelField = captureModel.fields[field.id];
    const { metaData: { title, description }, omekaMetaData: { purpose } } = captureModelField;

    const dataSet = mapFieldsToDataSet([captureModel.fields[field.id]], {
      [field.id]: field.input
    });

    dataSet['@context'] = dataSet['@context'] ? dataSet['@context'] : {};
    if (title) {
      dataSet['@context'].Title = 'http://dublincore.org/documents/dcmi-terms/#terms-title';
      dataSet.Title = title;
    }
    if (description && description !== title) {
      dataSet['@context'].Description = 'http://dublincore.org/documents/dcmi-terms/#terms-description';
      dataSet.Description = description;
    }

    return new Annotation(
      null,
      field.input,
      new AnnotationBodyList(
        new AnnotationBody(
          null,
          JSON.stringify(dataSet),
          'text/plain',
          'Dataset',
          purpose ? purpose.id : null,
        )
      ),
      AnnotationSelector.fromTarget(target, field.selector)
    );
  });
}

export function getLabelFromDraft(labelParts, captureModel, draft): string {
  if (!(
      labelParts &&
      labelParts.label
    )) {
    return null;
  }
  const fieldIds = Object.keys(draft.input);
  const fields = getModelFields(fieldIds, captureModel.fields);
  const labelField = labelParts.label;

  return fields.filter(e => e).reduce((label, model) => {
    if (label) return label;
    if (model.metaData.conformsTo && draft.input[model.id] && model.metaData.conformsTo.label === labelField) {
      return draft.input[model.id];
    }
    return label;
  }, '');
}

export function getSuitableLabelFromBody(bodies) {
  let label = 'unknown';
  const found = bodies.find(body => {
    if (
      body.type.indexOf('Text') !== -1 &&
      body.value
    ) {
      return true;
    }
  });
  return (found && found.value) ? found.value : label;
}

export function getMotivation(motivatedBy, captureModel, draft): Motivation {
  if (motivatedBy.id) {
    return {
      id: motivatedBy.id,
      label: motivatedBy.instance,
    };
  }
  return {
    id: 'oa:tagging',
    label: 'Tagging',
  };
}

export function convertPercentToPixel(props, height, width) {
  return {
    ...props,
    left: (props.x / 100) * width,
    top: (props.y / 100) * height,
    width: (props.width / 100) * width,
    height: (props.height / 100) * height,
    immutable: true,
    scale: 1,
    unit: 'pixel',
  };
}

export function createAnnotationFromCaptureModelAndDraft(target, captureModel, draft, customDraftSelector) {
  const bodies = [];
  let label = 'unknown';
  const draftSelector = customDraftSelector || draft.selector;

  let motivation = {
    id: 'oa:tagging',
    label: 'Tagging',
  };

  const {
    combine = false,
    externalise = true,
    humanReadable = false,
    serialize = true,
    labelParts,
    motivatedBy,
  } = captureModel.metaData;

  if (combine === false) {
    return createMultipleAnnotations(target, captureModel, draft);
  }

  if (combine) {
    createCombinedAnnotationBody(captureModel, draft).forEach(body => bodies.push(body));
  }

  if (externalise) {
    createExternalResourceAnnotationBody(captureModel, draft).forEach(body => bodies.push(body));
  }

  if (humanReadable) {
    createHumanReadableAnnotationBody(captureModel, draft).forEach(body => bodies.push(body));
  }

  if (serialize) {
    const serializedBody = createSerializedAnnotationBody(captureModel, draft);
    if (serializedBody) {
      bodies.push(serializedBody);
    }
  }

  if (labelParts) {
    label = getLabelFromDraft(labelParts, captureModel, draft) || getSuitableLabelFromBody(bodies);
  }

  if (motivatedBy) {
    motivation = getMotivation(motivatedBy, captureModel, draft);
  }

  return new Annotation(
    null,
    label,
    AnnotationBodyList.fromJsonLD(bodies),
    AnnotationSelector.fromTarget(target, draftSelector),
    motivation,
  );
}



