import { get, labelId, parseSelectorTarget } from '../helpers';
import {
  BODY_TYPE,
  CONFORMS_TO,
  DESCRIPTION,
  ID,
  INPUT_AUTOCOMPLETE_URI,
  INPUT_OPTIONS,
  INPUT_TYPE,
  PURPOSE,
  RDFS_LABEL,
  SELECTOR,
  SELECTOR_VALUE,
  TITLE,
  TYPE,
  FIELD_GROUP
} from '../vocab';

export default class Field {

  type = 'field';

  getReference(parent) {
    return { parentId: parent.id, ...this };
  }

  constructor(id, inputType, annotationMetaData, omekaMetaData, metaData) {
    this.id = id;
    this.inputType = inputType;
    this.annotationMetaData = annotationMetaData;
    this.omekaMetaData = omekaMetaData;
    this.metaData = metaData;
  }

  isValidTarget(target) {
    if (target === 'canvas') {
      return true;
    }
    return !(
      this.metaData.selector &&
      this.metaData.selector.toLowerCase() !== 'wholecanvasselector'
    );
  }

  static fromJsonLD(jsonLd) {
    const $ = get(jsonLd);
    return new Field(
      $(ID),
      $(INPUT_TYPE, { [ID]: null })[ID],
      {
        options: $(INPUT_OPTIONS),
        autocomplete: $(INPUT_AUTOCOMPLETE_URI),
      },
      {
        type: $(TYPE),
        group: labelId(
          $(FIELD_GROUP)
        ),
        bodyType: labelId(
          $(BODY_TYPE),
        ),
        purpose: labelId(
          $(PURPOSE),
        ),
      },
      {
        title: $(RDFS_LABEL) || $(TITLE),
        description: $(DESCRIPTION),
        conformsTo: labelId(
          $(CONFORMS_TO),
        ),
        selector: $(SELECTOR) ? $(SELECTOR)[ID] : null,
        selectorValue: parseSelectorTarget($(SELECTOR_VALUE)),
      },
    );
  }

}



