import BEM from 'digirati-bem-js';
import React, {Component} from 'react';
import Geosuggest from 'react-geosuggest';
import {GoogleMap, Marker, withGoogleMap} from 'react-google-maps';
import './GoogleMapSearch.scss';

const style = BEM.block('google-map-search');

const GoogleMapDraggablePin = withGoogleMap(props => {
  return (
    <GoogleMap
      ref={props.onMapLoad}
      defaultZoom={11}
      center={ props.location }>
      <Marker draggable={true} onDragEnd={ props.dragEnd } position={ props.location }/>
    </GoogleMap>
  );
});

class GoogleMapSearch extends Component {

  state = {
    currentLocation: null,
    selected: false,
  };

  render() {
    const key = 'AIzaSyD2X9UAJcuDi_8mXF0ESXtZ9B9KbYcgFKs';
    const {value: valueAsString, metaData, preview, update} = this.props;
    const {selected} = this.state;
    const [lat, lng] = valueAsString ? valueAsString.split(',') : [];
    const value = (lat && lng) ? {lat: parseFloat(lat), lng: parseFloat(lng)} : null;

    if (selected || preview) {
      return (
        <div style={{background: '#FFF'}}>
          <h4 className={style.element('label')}>{metaData.title}</h4>
          <div className={style.element('description')}>
            {metaData.description}
          </div>
          <img width="300"
               onClick={() => this.setState({selected: null})}
               src={`http://maps.googleapis.com/maps/api/staticmap?key=${key}&size=300x200&scale=2&center=${selected.lat},${selected.lng}&markers=${selected.lat},${selected.lng}&zoom=11`}/>
        </div>
      );
    }
    return (
      <div className={style}>
        <h4 className={style.element('label')}>{metaData.title}</h4>
        <div className={style.element('description')}>
          {metaData.description}
        </div>
        <div className={style.element('container')}>
          <Geosuggest onSuggestSelect={(e) => update(`${e.location.lat},${e.location.lng}`)}/>
          {
            value ? (
              <div>
                <GoogleMapDraggablePin
                  onMapLoad={(map) => this.map = map}
                  dragEnd={({latLng}) => update(`${latLng.lat()},${latLng.lng()}`)}
                  containerElement={(
                    <div className={style.element('map-object')}/>
                  )}
                  mapElement={(
                    <div className={style.element('map-object').modifier('inner')}/>
                  )}
                  location={value}/>
                <button
                  className={style.element('button')}
                  onClick={() => this.setState({selected: value})}>
                  save
                </button>
              </div>
            ) : null
          }
        </div>
      </div>
    );
  }

}

export default GoogleMapSearch;



