import React, {PureComponent} from 'react';
import 'core-js/modules/es6.object.keys';
import {immutableRegionsFromAnnotations} from 'digirati-annotation-bridge/es/annotations';
import {richAnnotationList} from 'digirati-annotation-redux/es/query/elucidateQuery';
import {currentCanvas} from 'digirati-annotation-redux/es/query/manifestQuery';
import {resourceTemplateList} from 'digirati-annotation-redux/es/query/resourceQuery';
import {Canvas} from 'digirati-annotation-components';
import {connector} from 'digirati-annotation-redux';
import {selectorCompat} from 'digirati-annotation-redux/es/utility';
import Measure from 'react-measure';
import {withState} from 'recompose';

class Viewer extends PureComponent {

  setViewer(viewer) {
    if (this.viewer) {
      return;
    }
    this.viewer = viewer;
    this.props.setViewer(viewer);
  }

  render() {
    const MAX_HEIGHT = window.innerHeight - 250;

    const {
      image,
      selectedViewer,
      updateDimensions,
      editableRegions,
      dimensions,
      state,
      toggleable,
      drafts,
      currentDraftSelectors,
      updateSelector,
      editCurrentDraftSecondarySelector,
      commitToCurrentDraft,
      editCurrentDraftSelector,
      currentSelector,
    } = this.props;
    const VIEWER_WIDTH = dimensions.width;

    const regions = [
      ...editableRegions,
      ...immutableRegionsFromAnnotations(
        richAnnotationList(state),
        resourceTemplateList(state),
      ),
      ...currentDraftSelectors,
    ].map(region => selectorCompat(region));

    // @todo move to selector
    const selectedCanvas = currentCanvas(state);
    return (
      <Measure
        onMeasure={(dimensions) => updateDimensions(dimensions)}
      >
        <div>
          { selectedCanvas ? (
            <Canvas ref={ref => this.setViewer(ref)}
                    showAnnotationToggle={toggleable || true}
                    onUpdateSelector={updateSelector}
                    onSaveSelector={commitToCurrentDraft}
                    selector={currentSelector && currentSelector.type ? currentSelector : null}
                    selectedViewer={selectedViewer}
                    image={image}
                    canvas={selectedCanvas}
                    regions={regions}
                    displayWidth={VIEWER_WIDTH}
                    onEditRegion={(e) => {
                      if (e.isSecondary) {
                        if (!e.id) {
                          return;
                        }
                        editCurrentDraftSecondarySelector(e.id)
                      }
                      editCurrentDraftSelector();
                    }}
                    maxHeight={MAX_HEIGHT}/>
          ) : (
            <div>
              <img src={image.src}/>
            </div>
          ) }
        </div>
      </Measure>
    );
  }
}

export default connector(
  withState('dimensions', 'updateDimensions', {width: -1, height: -1})(
    /*onlyUpdateForKeys([
     'image',
     'selectedViewer',
     'drafts',
     'currentDraft',
     'updateSelector',
     'commitToCurrentDraft',
     'currentSelector',
     ])(*/Viewer/*)*/,
  ),
);



