import React, { Component, PureComponent } from 'react';
import { connector } from 'digirati-annotation-redux';
import { Router } from 'digirati-annotation-components';
import {
  getDraftFromCaptureModel,
} from 'digirati-annotation-bridge';
import { getResourceById, isAtRoot } from 'digirati-annotation-redux/es/query/resourceQuery';
import {
  convertPercentToPixel,
  createAnnotationFromCaptureModelAndDraft,
} from 'digirati-annotation-redux/es/lib/annotation/mapping';
import renderForm from './views/renderForm';
import { immediate } from './resourceEditor.utility';
import confirmationPage from './views/confirmationPage';
import resourceNavigation from './views/resourceNavigation';

class ResourceEditor extends PureComponent {

  // This can in theory be moved to annotation-redux, once tech debt addressed.
  createAnnotationToBeMovedToAnnotationRedux = (targetResource) => {
    const { currentResource, asyncPublishDraft, currentDraft, createAnnotation } = this.props;

    // Create our annotation.
    const annotation = createAnnotationFromCaptureModelAndDraft(targetResource, currentResource, currentDraft);

    // Make a single promise we can wait on for sending one or many annotations.
    const promise = Array.isArray(annotation) ?
      Promise.all(annotation.map(singleAnnotation => createAnnotation(singleAnnotation))) :
      createAnnotation(annotation);

    // Publish the draft after the annotations.
    const result = asyncPublishDraft(currentDraft.id, promise);

    return { result, annotation };
  };

  // This will replace createAnnotation
  createAnnotationWithoutTechDebt = () => {
    const { target, manifest, canvas, postSave } = this.props;
    const {result, annotation} = this.createAnnotationToBeMovedToAnnotationRedux(target === 'manifest' ? manifest : canvas);
    if (postSave) {
      postSave(
        result, annotation
      );
    }
  };

  createAnnotation = () => {
    const { currentResource, target, manifest, canvas, asyncPublishDraft, currentDraft, createAnnotation, postSave } = this.props;
    const targetResource = target === 'manifest' ? manifest : canvas;
    this.resolveSelector(target, targetResource, currentDraft).then(draft => {
      // Create our annotation.
      const annotation = createAnnotationFromCaptureModelAndDraft(targetResource, currentResource, draft);

      // Make a single promise we can wait on for sending one or many annotations.
      const promise = Array.isArray(annotation) ?
        Promise.all(annotation.map(singleAnnotation => createAnnotation(singleAnnotation))) :
        createAnnotation(annotation);

      // Publish the draft after the annotations.
      const result = asyncPublishDraft(currentDraft.id, promise);

      // Run our post save.
      if (postSave) {
        postSave(result, annotation);
      }
    });
  };


  hasMultipleSelectorsWithPercentages(currentDraft) {
    let hasSelectors = false;
    try {
      hasSelectors = Object.keys(
        currentDraft.selectors,
      ).map(k => currentDraft.selectors[k])
      .reduce((state, action) => !!(state || (action.type && action.unit === 'percent')), false);
    } catch (e) {
      return false;
    }
    return hasSelectors;
  }

  resolveSelector(target, targetResource, draft) {
    const selector = draft.selector;

    if (target === 'manifest' || !selector) {
      return Promise.resolve({ ...draft, selector: targetResource });
    }

    // ########################################################################
    //       !     Everything below this is technical debt.       !           #
    // ########################################################################
    // In order to remove this we need to be sure that
    // in a draft all selectors, both on the draft and the
    // draft fields are always pixel values, and not percentages.
    // work has been done, but the previous code was not removed, so
    // I've added this in to replace it, and tried to make sure that
    // it doesn't execute if its doesn't need to. I've also added console
    // warnings for us to check during execution.
    // The problem with this code is the dependency on the current state of the
    // viewer. We need to get the pixel sizes from the viewer in order to resolve
    // percent values.
    // For these reasons it did not make it into the annotation-redux package,
    // and should be verified that it is not being used, and removed, including
    // the whole resolve selector method itself.
    // See also: createAnnotationWithoutTechDebt()
    if (selector.unit === 'percent') {
      console.warn('ResourceEditor', 'Percentage selectors should not reach drafts, please report.');
      const { withViewer } = this.props;
      return new Promise(function (resolve, err) {
        withViewer(viewer => {
          const { height, width } = viewer.getImageSize();
          resolve(
            this.resolveSelector({
              ...draft,
              selector: convertPercentToPixel(selector, height, width),
            })
          );
        });
      });
    }

    if (this.hasMultipleSelectorsWithPercentages(draft)) {
      console.warn('ResourceEditor', 'Percentage selectors should not reach draft fields, please report.');
      const { withViewer } = this.props;
      return new Promise(function (done, err) {
        withViewer(viewer => {
          const { height, width } = viewer.getImageSize();
          const draftFieldSelectors = Object.keys(draft.selectors).reduce((selectors, key) => {
            selectors[key] = convertPercentToPixel(draft.selectors[key], height, width);
          }, {});
          done({ ...draft, selectors: draftFieldSelectors });
        });
      });
    }
    // ########################################################################
    //           !          End of technical debt.           !                #
    // ########################################################################

    return Promise.resolve(draft);
  }

  render() {
    const {
      // Variables
      currentResourceTemplate,
      currentDraft,
      resourceForm,
      tree,
      isConfirmingDraft,
      isSelecting,
      renderResourceEditor,
      importCaptureModel,

      // Actions
      updateDraft,
      unpreviewDraft,
      confirmDraft,
      previewDraft,
      discardCurrentDraft,
      createDraft,
      isSelectingSecondary,
      editCurrentDraftSelector,
      editCurrentDraftSecondarySelector,
      editCurrentDraftFieldSelector,
      state,
      currentResource,

      moveForward,
      moveBack,
      reset,

      // UI Options
      hideTitle = false,
      draftDefaults = {},
    } = this.props;

    if (currentResource.choice && Object.values(currentResource.choice).length === 0) {
      console.warn('ResourceEditor', 'This should probably never happen, please report.');
      return <div/>; // Nothing for us to render!
    }

    // ################################################
    //               New proposed API                 #
    // ################################################
    /*
    return (
      <Router {...props}>
        <Branch match={(props) => (
          currentDraft &&
          currentDraft.template !== currentResource.id
        )}>
          <div>Matched this route.</div>
        </Branch>
      </Router>
    )

     */
    return (
      <div>
        <Router routes={[
          {
            matches: () => (
              currentDraft &&
              currentDraft.template !== currentResource.id
            ),
            render: () => <div>Please finish the current annotation you are creating.</div>,
          },
          {
            matches: () => currentResource.choice,
            render: () => resourceNavigation({
              showBackButton: isAtRoot(tree, state) === false,
              resourceTemplates: currentResource.choice,
              moveForward: (id) => moveForward(tree, id),
              moveBack: (id) => moveBack(tree),
              createDraft: rt => {
                const { resources, motivation, selectors } = getDraftFromCaptureModel(
                  getResourceById(state, rt.id),
                );
                createDraft(resources, rt.id, motivation || 'tagging', selectors, draftDefaults);
              },
            }),
          },
          {
            matches: () => isConfirmingDraft,
            render: () => confirmationPage({ onConfirm: () => reset(tree) && confirmDraft() }),
          },
          {
            matches: () => currentResourceTemplate && resourceForm.fields,
            render: () => renderForm({
              currentResourceTemplate,
              resourceForm,
              currentDraft,
              renderResourceEditor,
              importCaptureModel,
              previewDraft,
              updateDraft,
              onSave: this.createAnnotation,
              unpreviewDraft,
              isSelecting,
              isSelectingSecondary,
              goBack: () => {
                if (
                  isSelecting || (
                    (
                      currentDraft &&
                      currentDraft.selector &&
                      currentDraft.selector.type) || ''
                  ).toLowerCase() === 'wholecanvasselector'
                ) {
                  moveBack(tree) && discardCurrentDraft();
                } else {
                  editCurrentDraftSelector();
                }
              },
              editFieldSelector: editCurrentDraftSecondarySelector,
              currentTree: currentResource.id,
              elementEvents: {
                onFocus: id => editCurrentDraftFieldSelector(id, {
                  focused: true,
                }),
                onBlur: id => editCurrentDraftFieldSelector(id, {
                  focused: false,
                }),
              },
              hideTitle,
            }),
          },
          {
            matches: () => currentResource,
            render: () => {
              const Choose = immediate('loading...', () => {
                const { resources, motivation, selectors } = getDraftFromCaptureModel(
                  getResourceById(state, currentResource.id),
                );
                createDraft(resources, currentResource.id, motivation || 'tagging', selectors, draftDefaults);
              });
              return <Choose/>;
            },
          },
          {
            matches: () => true,
            render: () => (
              <div>
                <strong>Loading, or debugging...</strong>
              </div>
            ),
          },
        ]}/>
      </div>
    );
  }

}

export const ConnectedResourceEditor = connector(ResourceEditor);

export default class extends Component {
  render() {
    return (
      <ConnectedResourceEditor renderResourceEditor={
        (captureModel, postSave, ownProps) => (
          <ConnectedResourceEditor
            tree={captureModel}
            postSave={postSave}
            {...ownProps}/>
        )
      } {...this.props}/>
    );
  }
};



