import React, {PureComponent} from 'react';
import ResourceEditor from 'digirati-annotation-plugin-resource-editor/es/resourceEditor';
import {connector} from 'digirati-annotation-redux';
import {createDraftFromCaptureModel} from 'digirati-annotation-bridge';
import BEM from 'digirati-bem-js';

const $style = BEM.block('transcriber');

class Transcriber extends PureComponent {

  createDraft = (draftDefaults) => () => {
    const {createDraftFromCaptureModel, tree} = this.props;
    createDraftFromCaptureModel(tree, tree, draftDefaults);
  };

  render() {
    const { transcriptionVocab = 'dcterms:description', currentDraft, transcription, ...props } = this.props;
    if (!currentDraft) {
      return (
        <div className={$style}>
          <button className={$style.element('edit')} onClick={this.createDraft({ [transcriptionVocab]: transcription })}>edit</button>
          <div className={$style.element('transcription')}>
            { transcription }
          </div>
        </div>
      );
    }

    return (
      <div className={$style.modifier('editing')}>
        <ResourceEditor draftDefaults={{ [transcriptionVocab]: transcription }} hideTitle={true} {...props}/>
      </div>
    );
  }
}

export default connector(Transcriber, {createDraftFromCaptureModel});



