import { getCurrentDraft } from './draftQuery';

export function getResourceById(state, id) {
  return state.resource.list[id];
}

export function applyDefaultsToDraft(state, template, input, draftDefaults) {
  const form = getForm(getResourceById(state, template));
  if (!form || !form.fields) {
    return input;
  }

  const fields = Object.values(form.fields).reduce((s, f) => (s[f.id] = f ) && s, {});
  const draftDefaultKeys = Object.keys(draftDefaults);
  const filledInputs = Object.keys(input).reduce((input, url) => {
    if (
      fields &&
      fields[url] &&
      fields[url].metaData &&
      fields[url].metaData.conformsTo &&
      fields[url].metaData.conformsTo.label &&
      draftDefaultKeys.indexOf(fields[url].metaData.conformsTo.label) !== -1
    ) {
      input[url] = draftDefaults[fields[url].metaData.conformsTo.label];
    }
    return input;
  }, input);

  return {...input, ...filledInputs};
}

export function getCurrentResource(tree, state) {
  if (!(
      state &&
      state.resource &&
      state.resource.currentPaths
    )) {
    return null;
  }

  const value = state.resource.currentPaths[tree];

  if (!value) {
    return getResourceById(state, tree);
  }

  if (value && value.length === 0) {
    return state.resource.trees[tree];
  }

  const node = value.reduce((curState, path) => curState.choice[path], state.resource.trees[tree]);

  if (!node) {
    console.error('Invalid path', value, state.resource.trees[tree]);
    return null;
  }

  // @todo add isReference field to pick up.
  if (node.type === 'captureModel') {
    return state.resource.list[node.id];
  }

  return node;
}

export function isAtRoot(tree, state) {
  if (!(
      state &&
      state.resource &&
      state.resource.currentPaths &&
      state.resource.currentPaths[tree]
    )) {
    return true; // default to true to hide UI.
  }

  return (state.resource.currentPaths[tree].length === 0);
}

export function hasLandedOnForm(tree, state) {
  const currentItem = getCurrentResource(state, tree);
  if (!currentItem) {
    return false;
  }
  return currentItem.type === 'captureModel';
}

export function getCurrentResourceTemplate(state, scope) {
  const currentDraft = getCurrentDraft(state, scope);
  return (currentDraft && currentDraft.template) ? state.resource.list[currentDraft.template] : null;
}

export function getForm(resourceTemplate) {
  if (!resourceTemplate) {
    return null;
  }
  const { id, fields, ...meta } = resourceTemplate;
  return {
    id,
    fields,
    meta: { ...meta, ...meta.metaData },
  }
}

export function resourceTemplateList(state) {
  const list = state.resource.list;
  const keys = Object.keys(list);
  if (!keys[0]) {
    return [];
  }
  return list[keys[0]];
}

export function getCurrentPath(obj, state) {
  return state.resource.currentPath.reduce((o, i) => o[i], obj);
}



