import { BOX_SELECTOR, WHOLE_CANVAS_SELECTOR } from './vocab';
import AnnotationBody from './annotationBody';
import { AnnotationSelector } from 'digirati-annotation-redux/es/lib/elucidate/index';

/**
 * @deprecated
 */
export function parseTarget(target, scale = 1) {
  return AnnotationSelector.parse(target, scale);
}

export function mapTarget(target, scale = 1) {
  const boxSelector = parseTarget(target, scale);
  if (!boxSelector || typeof boxSelector.target === 'string') {
    return { type: WHOLE_CANVAS_SELECTOR, target: (boxSelector && boxSelector.target) || '' };
  }
  boxSelector.type = BOX_SELECTOR;
  return boxSelector;
}

export function resourceTemplateReverseTypeLookup(fields, key) {
  for (let template of Object.values(fields)) {
    if (template.metaData.conformsTo.label === key) {
      return template;
    }
  }
  return null;
}

export function mapAnnotationBody(resourceTemplate, bodyFields) {
  const fields = Object.keys(bodyFields);
  const fieldMap = {};

  fields.forEach(field => {
    const template = resourceTemplateReverseTypeLookup(resourceTemplate, field);
    if (template) {
      fieldMap[template.id] = bodyFields[field];
    }
  });

  return fieldMap;
}

export function createDraftListFromAnnotations(annotations, resourceTemplate, scale) {
  return annotations.map(anno => createDraftFromAnnotation(resourceTemplate, anno, scale));
}

export function immutableRegionsFromDraftList(drafts) {
  return Object.keys(drafts)
    .filter(k => drafts[k] && drafts[k].selector)
    .map(k => drafts[k])
    .filter(anno => anno && anno.selector)
    .map(anno => {
      const r = anno.selector.target;
      return {
        id: anno.id,
        onClick: () => {
        },
        left: r.x, // should be r.left
        top: r.y, // should be r.top
        height: r.height,
        width: r.width,
        scale: r.scale,
        selected: false,
        editing: false,
        immutable: true,
      }
    });
}

export function immutableRegionsFromAnnotations(annotations, resourceTemplate, scale) {
  return immutableRegionsFromDraftList(
    createDraftListFromAnnotations(annotations, resourceTemplate, scale),
  );
}

export function editableRegionsFromDraftList(drafts,
  currentDraftKey = null,
  onClickRegion = () => null,
  currentSelectorExists = true,) {
  return Object.keys(drafts)
    .filter(k => drafts[k] && drafts[k].selector)
    .map(k => {
      const r = drafts[k].selector;
      return {
        id: drafts[k].id,
        onClick: () => onClickRegion(k),
        selected: `${k}` === `${currentDraftKey}`,
        editing: `${k}` === `${currentDraftKey}` && currentSelectorExists,
        immutable: false,
        ...r
      }
    });
}

export function detectTypeOfAnnotation(annotation) {
  if (annotation.body && annotation.body.type === 'SpecificResource') {
    return 'SPECIFIC_RESOURCE';
  }
  if (annotation.body && annotation.body.value && typeof annotation.body.value === 'string') {
    // @todo temporary type.
    return 'ANNOTATION_STUDIO';
  }
  if (Object.prototype.toString.call(annotation.body) === '[object Array]') {
    return 'ANNOTATION_ARRAY';
  }
  return 'UNKNOWN';
}

export function getAnnotationBody(annotation) {
  const type = detectTypeOfAnnotation(annotation);

  switch (type) {
    case 'ANNOTATION_STUDIO':
      return JSON.parse(annotation.body.value);
    case 'SPECIFIC_RESOURCE':
    default:
      return AnnotationBody.fromBody(annotation.body);
  }
}

export function mapTargetToSelector(target) {
  return { ...target.target, ...target };
}

export function createDraftFromAnnotation(resourceTemplate, annotation, scale = 1) {
  if (!(
      resourceTemplate &&
      resourceTemplate.fields
    )) {
    return null;
  }
  return {
    id: annotation.id,
    input: mapAnnotationBody(
      resourceTemplate.fields,
      getAnnotationBody(annotation),
    ),
    selector: mapTargetToSelector(mapTarget(annotation.target, scale)),
    isPublishing: true,
    isPreviewing: true,
    published: true,
    template: resourceTemplate.id,
  };
}



