import React from 'react';
import {render} from 'react-dom';
import {utils} from 'digirati-annotation-plugin-core';
import ViewerProvider from './viewer.provider';

export default function ViewerPlugin($el, {
  viewer,
  height,
  width,
  toggleable,
  ...unknownProps
}, store) {
  utils.unknownPropertyWarning(unknownProps);

  const image = {
    src: $el.querySelector('img').src,
    height: parseInt(height, 10) || 0,
    width: parseInt(width, 10) || 0,
  };

  render(<ViewerProvider toggleable={toggleable} selectedViewer={viewer} image={image} store={store}/>, $el);
}



