import {
  addSelectorToDraft,
  addSelectorToDraftFieldFromViewer,
  addSelectorToDraftFromViewer,
  discardCurrentDraft,
  removeSelectorFromDraft,
  removeSelectorFromDraftField,
} from './drafts';
import { getCurrentDraft, getDraftById } from '../query/draftQuery';
import { selectorCompat } from '../utility';
import { withViewer } from './viewer';
import { getCurrentSelector } from '../query/selectorQuery';

export const SELECTOR_SET_AVAILABLE = 'SELECTOR_SET_AVAILABLE';
export const SELECTOR_CHOOSE = 'SELECTOR_CHOOSE';
export const SELECTOR_UPDATE = 'SELECTOR_UPDATE';
export const SELECTOR_COMMIT_TO_DRAFT = 'SELECTOR_COMMIT_TO_DRAFT';
export const SELECTOR_COMMIT_TO_DRAFT_FIELD = 'SELECTOR_COMMIT_TO_DRAFT_FIELD';
export const SELECTOR_CANCEL = 'SELECTOR_CANCEL';

export function setAvailableSelectors(selectors) {
  return { type: SELECTOR_SET_AVAILABLE, payload: { selectors } };
}

export function chooseSelector(type, source, defaults, done) {
  // @todo find better way of Canvas relative vs Viewport relative inspections
  if (!defaults || type.toLowerCase() !== 'madoc:boxdraw') {
    return (dispatch) => {
      dispatch({
        type: SELECTOR_CHOOSE,
        payload: { type, source, defaults: selectorCompat(defaults) },
      });
      if (type.toLowerCase() === 'wholecanvasselector') {
        dispatch(commitToCurrentDraft(false));
      }
      if (done) {
        done();
      }
    };
  }
  return withViewer((Canvas, dispatch) => {
    const { x, y } = Canvas.createViewportPoint(defaults.x, defaults.y);
    const { x: maxX, y: maxY } = Canvas.createViewportPoint(defaults.x + defaults.width, defaults.y + defaults.height);
    dispatch({
      type: SELECTOR_CHOOSE,
      meta: { fromViewer: true },
      payload: { type, source, defaults: selectorCompat({ ...defaults, x, y, width: maxX - x, height: maxY - y }) },
    });
    if (done) {
      done();
    }
  });
}

export function cancelSelector() {
  return { type: SELECTOR_CANCEL };
}

export function chooseSecondarySelector(id, source, type, defaults, done) {
  return chooseSelector(type, source, { ...selectorCompat(defaults), id, secondary: true }, done);
}

export function updateSelector(data) {
  return { type: SELECTOR_UPDATE, payload: { data: selectorCompat(data) } };
}

export function commitToCurrentDraftField(fieldId) {
  return (dispatch, getState) => {
    const state = getState();
    const selector = getCurrentSelector(state);
    dispatch(
      addSelectorToDraftFieldFromViewer(
        selector.source.draft,
        selectorCompat(selector),
        fieldId,
        () => dispatch({ type: SELECTOR_COMMIT_TO_DRAFT_FIELD }),
      ),
    );
  };
}

export function editCurrentDraftSecondarySelector(scope, fieldId) {
  return (dispatch, getState) => {
    const { id, selectors, template } = getCurrentDraft(getState(), scope);
    if (id && selectors && selectors[fieldId]) {
      dispatch(
        chooseSecondarySelector(
          fieldId,
          { template: template, draft: id, scope},
          selectors[fieldId].type,
          selectorCompat(selectors[fieldId]),
          // Remove only after choosing.
          () => dispatch(removeSelectorFromDraftField(id, fieldId)),
        ),
      );
    }
  };
}

export function editCurrentDraftSelector(scope) {
  return (dispatch, getState) => {
    const { id, selector, template } = getCurrentDraft(getState(), scope);
    if (id && selector) {
      if (selector.type === 'WholeCanvasSelector') {
        return dispatch(discardCurrentDraft(scope));
      }
      dispatch(
        chooseSelector(
          selector.type,
          { template: template, draft: id, scope },
          selectorCompat(selector),
          () => dispatch(removeSelectorFromDraft(id)),
        ),
      );
    }
  };
}

export function commitToCurrentDraft(isViewer = true) {
  return (dispatch, getState) => {
    const state = getState();
    const selector = getCurrentSelector(state);
    const currentDraft = getDraftById(state, selector.source.draft);
    if (selector.id) {
      return dispatch(commitToCurrentDraftField(selector.id));
    }
    if (isViewer) {
      dispatch(
        addSelectorToDraftFromViewer(
          currentDraft.id,
          selectorCompat(selector),
          () => dispatch({ type: SELECTOR_COMMIT_TO_DRAFT }),
        ),
      );
    } else {
      dispatch(addSelectorToDraft(currentDraft.id, selector));
      dispatch({ type: SELECTOR_COMMIT_TO_DRAFT });
    }
  };
}



