export const MANIFEST_ADD = 'MANIFEST_ADD';
export const MANIFEST_SELECT = 'MANIFEST_SELECT';
export const MANIFEST_REMOVE = 'MANIFEST_REMOVE';
export const MANIFEST_REQUEST = 'MANIFEST_REQUEST';
export const MANIFEST_CANVAS_SELECT = 'MANIFEST_CANVAS_SELECT';

export function requestManifest(url, select = true) {
  return dispatch => {
    dispatch({ type: MANIFEST_REQUEST, payload: { url } });
    return fetch(url)
      .then(response => response.json())
      .then((body) => {
        dispatch(addManifest(url, body));
        if (select) {
          dispatch(selectCanvas(url));
        }
        return body;
      });
  }
}

export function selectCanvas(url) {
  return { type: MANIFEST_CANVAS_SELECT, payload: { url } };
}

export function selectManifest(url) {
  return { type: MANIFEST_SELECT, payload: { url } };
}

export function addManifest(url, manifest) {
  return { type: MANIFEST_ADD, payload: { url, manifest } };
}

export function removeManifest(url) {
  return { type: MANIFEST_REMOVE, payload: { url } };
}



