export function getCurrentSelector(state) {
  return state.selector.currentSelector;
}

export function isSelecting(state) {
  return (
    state.selector &&
    state.selector.currentSelector &&
    state.selector.currentSelector.type
  );
}

export function currentSelectorIsSecondary(state) {
  return (
    isSelecting(state) &&
    state.selector.currentSelector.secondary &&
    state.selector.currentSelector.secondary === true
  );
}



