export const HELP_INFO_ADD = 'INFO_ADD';
export const HELP_INFO_REMOVE = 'INFO_REMOVE';
export const HELP_CONTEXT_ADD = 'HELP_CONTEXT_ADD';
export const HELP_CONTEXT_REMOVE = 'HELP_CONTEXT_REMOVE';

export function addInfo(key, content, type = 'info') {
  return { type: HELP_INFO_ADD, payload: { key, content, type } };
}

export function removeInfo(key, type = 'info') {
  return { type: HELP_INFO_REMOVE, payload: { key, type } };
}

export function setContext(content) {
  return { type: HELP_CONTEXT_ADD, payload: { content } };
}

export function removeContext() {
  return { type: HELP_CONTEXT_REMOVE }
}



