import React, { Component } from 'react';
import BEM from 'digirati-bem-js';
import Selector from '../selectors/Selector';
import './Region.scss';
const style = BEM.block('region');

export default class Region extends Component {

  renderRegionBox() {
    const { id, type, x, y, width, height, editing, canChange, onChange, meta } = this.props;
    const { isSelected, isSecondary, focused } = meta;
    return (
      <div
        key={id}
        className={style}
        onClick={() => {
          let el = document.getElementById(id);
          if (el) {
            el.focus();
          }
        }}
        style={{
          top: y,
          left: x,
          width: width ? width : 100,
          height: height ? height : 100,
        }}>
        <div className={style.element('item').modifiers({
          focused,
          editing,
          selected: isSelected,
          [type.replace(/:/, '-')]: true,
        }, true)}/>
        { canChange ? (
          <button onClick={() => onChange({width, height, x, y, meta: { id, isSecondary } })}>edit</button>
        ) : null }
      </div>
    );
  }

  render() {
    const {
      // Variables
      id,
      type,
      x,
      y,
      width,
      height,

      // Flags
      isInitial,
      isChanging,
      canChange,
      pointerEvents,
      editing,

      // Scales
      pixelScale,
      canvasScale,

      // Event handlers
      onChange, // no needed?
      onEdit,
      onUpdate,
      onCommit,
      onClick,

      defaultX,
      defaultY,

      // Misc
      meta,
    } = this.props;

    // We never render whole canvas.
    if (type === 'WholeCanvasSelector') {
      return null;
    }

    const posX = isChanging ? (x ? x : defaultX) : x;
    const posY =  isChanging ? (y ? y : defaultY) : y;

    // TECH DEBT.
    if (type === 'madoc:boxdraw' && isChanging === false) {
      return this.renderRegionBox();
    }

    return (
      type &&
      <Selector
        id={id}
        type={type}
        drag={isChanging}
        x={posX}
        y={posY}
        width={width}
        height={height}
        editing={editing} // false
        onEdit={onEdit}
        onUpdate={onUpdate}
        onSave={onCommit}
        canvasScale={canvasScale}
        isSecondary={meta ? meta.isSecondary : false}
      />
    );
  }
}


