import React, { Component } from 'react';


export default function withFixedScale(WrappedComponent) {

  return class extends Component {
    render() {

      if (!this.props.canvasScale) {
        return <WrappedComponent {...this.props}/>
      }
      return (
        <WrappedComponent style={{ transform: `scale(${1/this.props.canvasScale})` }} {...this.props}/>
      );
    }
  }
}


