import md5 from 'blueimp-md5';
import { getCollectionId, getServer } from '../query/elucidateQuery';
import { createElucidateCollection, sendAnnotationToServer, postAnnotation } from '../lib/elucidate';
import Annotation from '../lib/annotation/Annotation';
export const ELUCIDATE_SET_SERVER = 'ELUCIDATE_SET_SERVER';
export const ELUCIDATE_SET_RESOURCE = 'ELUCIDATE_SET_RESOURCE';
export const ELUCIDATE_CREATE_COLLECTION = 'ELUCIDATE_CREATE_COLLECTION';
export const ELUCIDATE_SET_COLLECTION = 'ELUCIDATE_SET_COLLECTION';
export const ELUCIDATE_SEND_ANNOTATION = 'ELUCIDATE_SEND_ANNOTATION';
export const ELUCIDATE_ADD_ANNOTATION = 'ELUCIDATE_ADD_ANNOTATION';

export function initialiseElucidate(elucidateServer, resource, resourceLabel) {
  return (dispatch, state) => {
    if (!elucidateServer) {
      return null;
    }
    dispatch(setServer(elucidateServer));
    dispatch(setResource(resource));
    dispatch(createCollection(resourceLabel, resource));
  }
}

export function setServer(server) {
  return { type: ELUCIDATE_SET_SERVER, payload: { server } };
}

export function setResource(resource) {
  return { type: ELUCIDATE_SET_RESOURCE, payload: { resource } };
}

export function createCollection(label, resourceId) {
  return (dispatch, state) => {
    dispatch({ type: ELUCIDATE_CREATE_COLLECTION, payload: { label, resourceId } });
    const elucidateServer = getServer(state());
    return createElucidateCollection(elucidateServer, label, md5(resourceId)).then(
      collection => {
        dispatch(
          setCollection(resourceId, collection),
        );
        return collection;
      },
    );
  }
}

export function setCollection(resourceId, collection) {
  return {
    type: ELUCIDATE_SET_COLLECTION,
    payload: { resourceId, collection },
  };
}

export function addElucidateAnnotation(annotation) {
  return { type: ELUCIDATE_ADD_ANNOTATION, payload: { id: annotation.id, annotation } };
}

export function elucidateSendAnnotation(annotation: Annotation) {
  return {
    type: ELUCIDATE_SEND_ANNOTATION,
    payload: { annotation: annotation.toJSON() }
  };
}

export function createAnnotation(annotation: Annotation) {
  return (dispatch, state) => {
    dispatch(elucidateSendAnnotation(annotation));
    const collection = getCollectionId(state());
    return postAnnotation(collection, annotation).then(
      annotation =>
        dispatch(
          addElucidateAnnotation(annotation)
        )
    );
  }
}

/**
 * @deprecated
 */
export function sendAnnotation(body, target, motivation, label) {
  return (dispatch, state) => {
    dispatch({ type: ELUCIDATE_SEND_ANNOTATION, payload: { annotation: { body, target, motivation, label } } });
    const collection = getCollectionId(state());
    return sendAnnotationToServer(collection, body, target, motivation, label).then(
      annotation =>
        dispatch(
          addElucidateAnnotation(annotation),
        ),
    );
  }
}



