import React from 'react';
import {branch, compose, lifecycle, mapProps, pure, renderComponent, renderNothing} from 'recompose';
import BEM from 'digirati-bem-js';

const style = BEM.block('osd-viewer');

export function isValidTileSource(tileSource) {
  return !!(
    tileSource &&
    tileSource['@context'] &&
    tileSource['@id'] &&
    tileSource.protocol === 'http://iiif.io/api/image' &&
    tileSource.width &&
    tileSource.height &&
    tileSource.profile
  );
}

export function getCanonicalImage(image) {
  if (image.substr(-9, 9) === 'info.json') {
    return image;
  }
  if (image.substr(-1, 1) === '/') {
    return `${image}info.json`;
  }
  return `${image}/info.json`;
}

export function resolveRealTileSourceFromCanvas(canvas) {
  return resolveRealTileSource(
    canvas.images.map(image => image.resource.service || null).filter(e => e).pop(),
  );
}

export function resolveRealTileSource(image) {
  if (isValidTileSource(image)) {
    return Promise.resolve(image);
  }

  if (image['@id']) {
    return fetch(getCanonicalImage(image['@id']))
      .then(r => r.json())
      .then(tileSource => isValidTileSource(tileSource) ? tileSource : null);
  }

  return Promise.resolve(null);
}

export function createElement(isSelected, id, type) {
  const container = document.createElement('div');
  const $el = document.createElement('div');
  container.setAttribute('data-selected', isSelected);
  if (type) {
    container.setAttribute('data-selector-type', type);
  }
  container.id = `overlay-${id}`;
  $el.classList.add(
    ...style.element('region-item').modifiers({selected: isSelected}, false)
  );
  container.appendChild($el);
  return container;
}

export function withManifest(BaseComponent) {
  return compose(
    mapProps(props => ({
      regionIds: props.regions ? props.regions.map(region => region.id) : [],
      ...props
    })),
    lifecycle({
      componentWillMount() {
        const {canvas} = this.props;
        resolveRealTileSourceFromCanvas(canvas).then((tileSource) => this.setState({tileSource}));
      },
    }),
    branch(
      ({tileSource}) => tileSource,
      renderComponent(BaseComponent),
      renderComponent(() => <div>loading...</div>),
    ),
  )(renderNothing());
}



