import React, { PureComponent, PropTypes } from 'react';
import Rnd from 'react-rnd';
import selectorPropTypes from './_selectorPropTypes';
import BEM from 'digirati-bem-js';
import './BoxSelector.scss';

const style = BEM.block('box-selector');

class BoxSelector extends PureComponent {

  // static propTypes = {
  //   ...selectorPropTypes,
  //   onUpdate: PropTypes.func.isRequired,
  // };

  updateHeightWidth() {
    this.props.onUpdate({
      height: this.rnd.resizable.state.height,
      width: this.rnd.resizable.state.width,
      y: this.rnd.state.y,
      x: this.rnd.state.x,
    })
  }

  save() {
    this.props.onSave({
      height: this.rnd.resizable.state.height,
      width: this.rnd.resizable.state.width,
      y: this.rnd.state.y,
      x: this.rnd.state.x,
    });
  }

  getInitialPosition() {
    const { x, y, height, width } = this.props;

    return {
      x: x ? x : 100,
      y: y ? y : 100,
      width: width ? width : 100,
      height: height ? height : 100,
    };
  }

  render() {
    return (
        <Rnd ref={c => this.rnd = c }
             initial={this.getInitialPosition()}
             onResizeStop={() => this.updateHeightWidth()}
             onDragStop={() => this.updateHeightWidth()}
             className={style}
             bounds={'parent'}>
        <span style={{ color: '#000' }}>
          <button className={style.element('button')} onClick={(e) => this.save()}>Save</button>
        </span>
        </Rnd>
    );
  }
}

export default BoxSelector;



