import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';

import './css/style.scss';
import './css/base.scss';
import './css/grid.scss';
import './css/image-grid.scss';
import './css/collection-list.scss';
import './css/collection-item.scss';
import './css/topic-list.scss';
import './css/prev-next.scss';
import './css/pagination.scss';

import CorePlugin, {utils} from 'digirati-annotation-plugin-core';
import ViewerPlugin from 'digirati-annotation-plugin-viewer';
import ResourceEditorPlugin from 'digirati-annotation-plugin-resource-editor';
import TranscriberPlugin from 'digirati-annotation-plugin-transcriber';
import RegionAnnotation from './js/region-annotations';

if (process.env.NODE_ENV !== 'production') {
  const {whyDidYouUpdate} = require('why-did-you-update');
  let createClass = React.createClass;
  Object.defineProperty(React, 'createClass', {
    set: (nextCreateClass) => {
      createClass = nextCreateClass;
    }
  });
  // whyDidYouUpdate(React, { exclude: /^Core|Measure/ })
}

const behaviours = resolveSharedStore => ({
  'annotation-studio-core': (image, attributes) => CorePlugin(image, attributes).then(store => resolveSharedStore(store)),
  'annotation-studio-viewer': ViewerPlugin,
  'annotation-studio-resource-editor': ResourceEditorPlugin,
  'annotation-studio-transcriber': TranscriberPlugin,
  'annotation-studio-region-annotations': ($el, attributes, store) => {
    return render((
      <Provider store={store}>
        <RegionAnnotation />
      </Provider>
    ), $el);
  },
});

const { resolve, promise: whenToRender } = utils.deferred();

document.addEventListener("DOMContentLoaded", () => {
  // Small error-prone example of data behaviour factory.
  for (const $el of document.querySelectorAll('[data-behaviour]')) {
    const register = behaviours(resolve);
    try {
      const attribute = $el.getAttribute('data-behaviour');
      const behaviour = register[attribute];
      if (behaviour.length === 3) {
        whenToRender.then(store => behaviour($el, utils.getAttributes($el), store));
      } else {
        register[$el.getAttribute('data-behaviour')]($el, utils.getAttributes($el));
      }
    } catch (e) {
      console.error('Behaviour does not exist: ' + $el.getAttribute('data-behaviour'), e);
    }
  }
});

export default {};



