import BEM from 'digirati-bem-js';
import React, {Component} from 'react';
import './DropDown.scss';

const style = BEM.block('input-drop-down');

// Source: https://jsfiddle.net/11szrbx9/
function printf(format, ...values) {
  return values.reduce((str, val) => str.replace(/%s/, val), format);
}

class DropDown extends Component {
  constructor(props) {
    super(props);

    this.state = {
      suggestions: []
    }
  }

  selectSuggestion(suggestion) {
    this.props.update(suggestion.uri);
  }

  updateSuggestions(value) {
    this.props.update(value)
    fetch(
      printf(this.props.autocomplete, value, 'something') // second ones the class, maybe needs changed.
    )
    .then(f => f.json())
    .then(data => this.setState({ suggestions: data.completions })); // probably needs changed?
  }

  renderBox() {
    const {id, value, options, autocomplete, preview, update} = this.props;
    const {suggestions} = this.state;

    if (preview) {
      return <div className={style.element('preview')}>{value}</div>;
    }

    if (autocomplete) {
      return <div>
        <input onChange={(e) => this.updateSuggestions(update, e.currentTarget.value)} type="text" value={value || ''} ref={ref => this.input = ref}/>
        {suggestions ?
          <ul>
            { suggestions.map(suggestion => <li key={suggestion.uri} onClick={() => this.selectSuggestion(suggestion)}>{suggestion.label}</li>) }
          </ul>
          : null }
      </div>;
    }

    return (
      <select
        className={style.element('input')}
        id={id}
        value={value || ''}
        onChange={e => update(e.currentTarget.value)}>
        { options.map((value, n) => <option key={n} value={value}>{value}</option>) }
      </select>
    );
  }

  render() {
    const { id, metaData } = this.props;
    return (
      <div className={style}>
        <label className={style.element('label')} htmlFor={id}>
          {metaData.title} {' '}
          { (metaData.selector && metaData.selectorValue) ?
            (<a onClick={(e) => {
              e.preventDefault();
              displayFieldSelector(id);
            }} href="#">edit</a>)
            : null }
        </label>
        <div className={style.element('description')}>
          {metaData.description}
        </div>
        { this.renderBox() }
      </div>
    );
  }

}

export default DropDown;



