import React from 'react';
import BEM from 'digirati-bem-js';
import './button.scss';

const $confirmationPage = BEM.block('confirmation-page');
const $button = BEM.block('button');

export default function confirmationPage({onConfirm}) {
  return (
    <div className={$confirmationPage}>
      <h3 className={$confirmationPage.element('title')}>Your annotation has been added to the photo.</h3>
      <p className={$confirmationPage.element('copy')}>Do you want to add another?</p>
      <button className={$button.modifier('confirmation')} onClick={onConfirm}>
        Start again
      </button>
    </div>
  );
}



