import {
  DRAFT_CONFIRM,
  DRAFT_CREATE,
  DRAFT_DESELECT,
  DRAFT_DISCARD,
  DRAFT_PREVIEW,
  DRAFT_PUBLISHED,
  DRAFT_PUBLISHING,
  DRAFT_REMOVE_SELECTOR,
  DRAFT_SELECT,
  DRAFT_UNPREVIEW,
  DRAFT_UPDATE_FIELD_SELECTOR,
  DRAFT_UPDATE_INPUT,
  DRAFT_UPDATE_SELECTOR,
} from '../actions/drafts';
import update from 'immutability-helper';

const defaultState = {
  currentDrafts: {},
  isConfirmed: {},
  list: {},
};

function draftModel(draft) {
  return {
    id: draft.id,
    input: draft.input || {},
    selectors: draft.selectors || {},
    template: draft.template,
    motivation: draft.motivation,
    isPublishing: draft.isPublishing || false,
    isPreviewing: draft.isPreviewing || false,
    selector: draft.selector || null,
  };
}

export default function draftReducer(state = defaultState, action) {
  switch (action.type) {
    case DRAFT_CREATE:
      return update(state, {
        list: {
          [action.payload.id]: {
            $set: draftModel(action.payload),
          },
        },
      });

    case DRAFT_UPDATE_FIELD_SELECTOR:
      return update(state, {
        list: {
          [action.payload.id]: {
            selectors: { $merge: action.payload.selectors },
          },
        },
      });

    case DRAFT_UPDATE_INPUT:
      return update(state, {
        list: {
          [action.payload.id]: {
            input: { $merge: action.payload.fields },
          },
        },
      });

    case DRAFT_REMOVE_SELECTOR:
      return update(state, {
        list: {
          [action.payload.id]: {
            selector: { $set: null },
          },
        },
      });

    case DRAFT_UPDATE_SELECTOR:
      return update(state, {
        list: {
          [action.payload.id]: {
            selector: { $set: action.payload.selector ? action.payload.selector : state.list[action.payload.id].selector },
          },
        },
      });

    case DRAFT_SELECT:
      return update(state, {
        currentDrafts: { [action.payload.scope]: { $set: action.payload.id } },
      });

    case DRAFT_DESELECT:
      return update(state, {
        currentDrafts: { [action.payload.scope]: { $set: null } },
      });

    case DRAFT_PUBLISHING:
      return update(state, {
        list: {
          [action.payload.id]: {
            isPublishing: { $set: true },
          },
        },
      });

    case DRAFT_PREVIEW:
      return update(state, {
        list: {
          [action.payload.id]: {
            isPreviewing: { $set: true },
          },
        },
      });

    case DRAFT_UNPREVIEW:
      return update(state, {
        list: {
          [action.payload.id]: {
            isPreviewing: { $set: false },
          },
        },
      });

    case DRAFT_CONFIRM:
      return update(state, {
        isConfirmed: { [action.payload.scope]: { $set: false } },
      });

    case DRAFT_PUBLISHED:
      return update(state, {
        currentDrafts: { [action.payload.scope]: { $set: null } },
        isConfirmed: { [action.payload.scope]: { $set: true } },
        list: {
          $apply: function (obj) {
            const { [action.payload.id]: _, ...copy } = obj;
            return copy;
          },
        },
      });

    case DRAFT_DISCARD:
      const listUpdate = {
        $apply: function (obj) {
          const { [action.payload.id]: _, ...copy } = obj;
          return copy;
        },
      };
      if (state.currentDrafts[action.payload.scope] !== action.payload.id) {
        return update(state, {
          list: listUpdate,
        });
      }
      return update(state, {
        currentDrafts: { [action.payload.scope]: { $set: null } },
        list: listUpdate,
      });


  }
  return state;
}



