import React from 'react';
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux';
import {
  addSelectorToDraftFromViewer,
  asyncPublishDraft,
  confirmDraft,
  createDraft,
  deselectDraft,
  discardCurrentDraft,
  discardDraft,
  editCurrentDraftFieldSelector,
  previewDraft,
  publishDraft,
  removeSelectorFromCurrentDraft,
  removeSelectorFromDraft,
  selectDraft,
  unpreviewDraft,
  updateDraft,
} from './actions/drafts';
import { addAnnotation } from './actions/annotations';
import {
  cancelSelector,
  chooseSecondarySelector,
  chooseSelector,
  commitToCurrentDraft,
  commitToCurrentDraftField,
  editCurrentDraftSecondarySelector,
  editCurrentDraftSelector,
  setAvailableSelectors,
  updateSelector,
} from './actions/selectors';
import {
  addElucidateAnnotation,
  createCollection,
  createAnnotation,
  sendAnnotation,
  setCollection,
  setResource,
  setServer,
} from './actions/elucidate';
import { moveBack, moveForward, reset } from './actions/resources';
import { currentDraftEditableRegions, getCurrentDraft, getDraftById } from './query/draftQuery';
import { getCurrentResource, getCurrentResourceTemplate, getForm, resourceTemplateList } from './query/resourceQuery';
import { addManifest, removeManifest, requestManifest, selectCanvas, selectManifest } from './actions/manifest';
import { richAnnotationList } from './query/elucidateQuery';
import { setViewer, withViewer } from './actions/viewer';
import { currentSelectorIsSecondary, isSelecting } from './query/selectorQuery';


function bindCurrentResource(currentResource, actions) {
  if (!currentResource) {
    return {};
  }
  return Object.keys(actions).reduce((state, key) => {
    state[key] = (...args) => actions[key](currentResource, ...args);
    return state;
  }, {});
}

export default (WrappedComponent, actions = {}) => connect((state, props) => {
  const annotationList = richAnnotationList(state);
  const currentResource = props.tree ? getCurrentResource(props.tree, state) : null;
  const currentResourceTemplate = props.tree ? getCurrentResourceTemplate(state, props.tree) : null;
  const currentDraft = props.tree ? getCurrentDraft(state, props.tree) : null;
  const currentSelector = (state.selector && state.selector.currentSelector) ? state.selector.currentSelector : null;

  // @todo move to query.
  const currentDraftList = Object.values(state.drafts.currentDrafts);
  const currentDraftSelectors = currentDraftList.length ? currentDraftList.reduce((selectors, currentDraftId) => {
    if (!currentDraftId) {
      return [];
    }
    const currentDraft = getDraftById(state, currentDraftId);
    if (!currentDraft) {
      return [];
    }
    Object.keys(currentDraft.selectors).forEach(k => {
      selectors.push({ ...currentDraft.selectors[k], id: k, isSecondary: true });
    });
    return selectors;
  }, []) : [];


  return {
    state,
    currentSelector,
    currentDraft,
    currentResource,
    isSelecting: isSelecting(state),
    isSelectingPrimary: (isSelecting(state) && currentSelectorIsSecondary(state) === false),
    isSelectingSecondary: (isSelecting(state) && currentSelectorIsSecondary(state) === true),
    isConfirmingDraft: props.tree ? state.drafts.isConfirmed[props.tree] === false : true,
    editableRegions: currentDraftEditableRegions(state),
    drafts: state.drafts.list,
    currentResourceTemplate,
    currentDraftSelectors,
    draftIsCurrentlySelecting: (!currentDraft || !currentDraft.selector) && currentSelector.type,
    draftHasSelector: (!currentDraft || !currentDraft.selector),
    resourceTemplateList: resourceTemplateList(state),
    wholeCanvasAnnotations: annotationList ? annotationList.filter(anno => anno.selector && !anno.selector.target.expanded) : [],
    regionAnnotations: annotationList ? annotationList.filter(anno => anno.selector && anno.selector.target.expanded) : [],
    resourceForm: currentResourceTemplate ? getForm(currentResourceTemplate) : null,
    resourceTemplates: state.resource,
  }
}, (dispatch, ownProps) => bindActionCreators({
  ...bindCurrentResource(ownProps.tree, {
    confirmDraft,
    deselectDraft,
    discardDraft,
    discardCurrentDraft,
    removeSelectorFromCurrentDraft,
    selectDraft,
    editCurrentDraftSecondarySelector,
    editCurrentDraftSelector,
    editCurrentDraftFieldSelector,
    unpreviewDraft,
    createDraft,
  }),
  commitToCurrentDraft,
  commitToCurrentDraftField,
  cancelSelector,
  chooseSecondarySelector,
  setAvailableSelectors,
  chooseSelector,
  updateSelector,
  getCurrentDraft,
  updateDraft,
  previewDraft,
  addSelectorToDraftFromViewer,
  removeSelectorFromDraft,
  setViewer,
  withViewer,
  setServer,
  createCollection,
  setCollection,
  sendAnnotation,
  createAnnotation,
  addElucidateAnnotation,
  addAnnotation,
  publishDraft,
  asyncPublishDraft,
  setResource,
  requestManifest,
  addManifest,
  removeManifest,
  selectCanvas,
  selectManifest,
  moveForward,
  moveBack,
  reset,
  ...actions
}, dispatch), null, { withRef: true })(WrappedComponent);



