import AnnotationBody from './AnnotationBody';

export default class AnnotationBodyList {

  constructor(list: Array<AnnotationBody>) {
    if (list instanceof AnnotationBody) {
      this.list = [list];
    } else {
      this.list = list || [];
    }
  }

  getOfType = (type): AnnotationBody => {
    return (this.list.find(function (item) {
      return item.type.indexOf(type) !== -1;
    }).value || null);
  };

  // Computed fields.
  get TextualBody(): AnnotationBody {
    return this.getOfType('TextualBody');
  }

  get Image(): AnnotationBody {
    return this.getOfType('Image');
  }

  get Video(): AnnotationBody {
    return this.getOfType('Video');
  }

  get Sound(): AnnotationBody {
    return this.getOfType('Sound');
  }

  get Text(): AnnotationBody {
    return this.getOfType('Text');
  }

  get SpecificResource(): AnnotationBody {
    return this.getOfType('SpecificResource');
  }

  get Dataset(): AnnotationBody {
    return JSON.parse(this.getOfType('Dataset'));
  }

  toJSON() {
    if (this.list.length === 1) {
      return this.list[0].toJSON();
    }
    return this.list.map(j => j.toJSON());
  }

  static fromJsonLD(body) {
    if (
      Object.prototype.toString.call(body) === '[object Object]' && body.value
    ) {
      return new AnnotationBodyList([
        AnnotationBody.fromJsonLD(body)
      ]);
    }

    if (Object.prototype.toString.call(body) !== '[object Array]') {
      return null;
    }

    return new AnnotationBodyList(body.map(anno => AnnotationBody.fromJsonLD(anno)));
  }
}



