/**
 * This is a port of: https://github.com/digirati-co-uk/omeka-elucidate-module/blob/master/src/Domain/AnnotationBody.php
 */

export type BodyType = 'Dataset' | 'Image' | 'Video' | 'Sound' | 'Text' | 'SpecificResource';
export type TextDirections = 'ltr' | 'rtl' | 'auto';

export default class AnnotationBody {

  id: string;
  format: string; // @todo add enum
  language: string;
  processingLanguage: string;
  textDirection: ?TextDirections;
  type: Array<BodyType>;

  constructor(
    id,
    value,
    format,
    type,
    purpose,
    language,
    processingLanguage,
    textDirection,
  ) {
    this.id = id;
    this.value = value;
    this.type = Array.isArray(type) ? type : [type];
    this.purpose = purpose;
    this.format = format;
    this.language = language;
    this.processingLanguage = processingLanguage;
    this.textDirection = textDirection;
  }

  static fromJsonLD(jsonLd) {
    return AnnotationBody.fromBody(jsonLd);
  }

  toJSON() {
    return {
      ...this,
      id: this.id === null ? undefined : this.id,
      value: this.value === null ? undefined : this.value,
      type: this.type.length === 1 ? this.type[0] : this.type,
    };
  }

  static fromBody(body) {
    return new AnnotationBody(
      body.id,
      body.value,
      body.format,
      body.type,
      body.purpose,
      body.language,
      body.processingLanguage,
      body.textDirection,
    );
  }
}



