import React from 'react';
import {branch, compose, renderComponent, renderNothing} from 'recompose';
import {withManifest} from './openseadragon/utility';
import OSDViewer from './openseadragon/viewer';
import withCanvas from './openseadragon/withCanvas';
import ScaledImageViewer from './ScaledImageViewer';
import StaticImageViewer from './StaticImageViewer';


export default compose(
  // @todo optimise this!
  // shouldUpdate(
  //   (props, nextProps) => (
  //     true ||
  //     props.type !== nextProps.type ||
  //     props.regions !== nextProps.regions ||
  //     props.tileSource !== nextProps.tileSource ||
  //     props.scale !== nextProps.scale
  //   ),
  // ),
  // Branches are conditional render. First parameter is a bool
  // the second argument is the truth-y and the third is the false-y
  branch(
    props => props.type === 'ScaledImageViewer',
    // Needs withStaticCanvas for interoperability
    // Needs to accept children that get drawn on.
    renderComponent(ScaledImageViewer),
    // I've nested the false-y to add another condition.
    branch(
      props => props.type === 'OpenSeadragonViewer',
      renderComponent(
          withManifest(
            withCanvas(OSDViewer)
          ),
      ),
      // Fallback.
      renderComponent(StaticImageViewer),
    ),
  ),
  // pure,
)(renderNothing());



