import 'whatwg-fetch';

const JSON_LD_HEADERS = {
  'Content-Type': 'application/ld+json',
  Accept: 'application/ld+json',
};

import type Annotation from '../annotation/Annotation';

export { AnnotationSelector } from './AnnotationSelector';

const CONTEXT = 'http://www.w3.org/ns/anno.jsonld';

export function makeRequest(method, uri, body, headers = []) {
  return fetch(uri, {
    method,
    headers: { ...JSON_LD_HEADERS, ...headers },
    body: JSON.stringify(body),
    credentials: 'same-origin',
  })
    .then(response => {
      if (response.status >= 200 && response.status < 300) {
        return response.json();
      }
      return {
        error: response.statusText,
        status: response.status,
      };
    });
}

export function get(uri, body) {
  return makeRequest('GET', uri, body);
}

export function post(uri, body, headers = []) {
  return makeRequest('POST', uri, body, headers);
}

export function getAnnotation(server, id) {
  return get(`${server}${id}/`);
}

export async function* getAllAnnotationsFromCollection(collectionResource) {
  const firstPage = collectionResource.first;
  if (!firstPage.items) {
    return;
  }
  // First page of results.
  for (let item of firstPage.items) {
    yield item;
  }
  // Further pages of results.
  if (firstPage.next) {
    const collection = fetchPageCollection(firstPage.next);
    do {
      const item = await collection.next();
      if (item.done) {
        return;
      }
      yield item.value;
    } while (true);
  }
}

async function* fetchPageCollection(resource) {
  const currentPage = await get(resource);
  if (currentPage.items) {
    for (let item of currentPage.items) {
      yield item;
    }
  }
  if (currentPage.next) {
    return fetchPageCollection(currentPage.next);
  }
}

export function createElucidateCollection(server, label, id) {
  return getAnnotation(server, id).then(response => {
    if (response.status === 404) {
      return post(server, {
        '@context': CONTEXT,
        type: 'AnnotationCollection',
        label,
      }, {
        Slug: id,
      });
    }
    // Digest collection
    return response;
  });
}

export function postAnnotation(collection : string, annotation: Annotation) {
  return post(collection, annotation);
}

/**
 * @deprecated
 */
export function sendAnnotationToServer(server, body, target, motivation, label) {
  return post(server, {
    '@context': CONTEXT,
    type: 'Annotation',
    label: label ? label : 'unknown',
    body,
    target,
    motivation,
  })
}



