import BEM from 'digirati-bem-js';
import React, { PureComponent } from 'react';
import './Form.scss';
import FormGroup from './FormGroup';

const style = BEM.block('input-form');

class Form extends PureComponent {

  renderGroups(draft, props, { render, groups }) {
    const groupKeys = [FormGroup.DEFAULT_GROUP, ...Object.keys(groups)];
    return groupKeys.map(key =>
      render[key].length !== 0 ? (
        <FormGroup
          key={key}
          groupKey={key}
          style={style}
          draft={draft}
          fields={render[key]}
          group={groups[key]} {...props}/>
      ) : null,
    );
  }

  render() {
    const { fields, draft, ...props } = this.props;

    return (
      <div>
        {this.renderGroups(
          draft,
          props,
          fields.reduce(
            ({ render, groups }, field) => {
              if (!field.omekaMetaData.group.id) {
                render[FormGroup.DEFAULT_GROUP].push(field);
                return { render, groups };
              }
              if (!groups[field.omekaMetaData.group.id]) {
                groups[field.omekaMetaData.group.id] = field.omekaMetaData.group;
                render[field.omekaMetaData.group.id] = [];
              }
              render[field.omekaMetaData.group.id].push(field);
              return { render, groups };
            }
            , { render: { defaultGroup: [] }, groups: {} }),
        )}
      </div>
    );
  }
}

export default Form;



