export const VIEWER_MUTATION = 'VIEWER_MUTATION';
export const VIEWER_ACCESS = 'VIEWER_ACCESS';

export function setViewer(viewer) {
  return { type: VIEWER_MUTATION, payload: { viewer } };
}

export function withViewer(func) {
  return { type: VIEWER_ACCESS, payload: { func } };
}


