import { LOGGING_LOG } from '../actions/logging';
import update from 'immutability-helper';

const defaultState = {
  messages: [],
};

export default function loggingReducer(state = defaultState, action) {
  if (action.type === LOGGING_LOG) {
    return update(state, {
      messages: { $push: [action.payload.message] },
    });
  }
  return state;
}



