import AnnotationSelector from './AnnotationSelector';
import AnnotationBodyList from './AnnotationBodyList';

export type Motivation = {
  id: string,
  label: string
}

export default class Annotation {


  // Serialized fields
  id: string;
  type = 'Annotation';
  label: string;
  body: AnnotationBodyList;
  target: AnnotationSelector|AnnotationSelector[];
  motivation: Motivation;


  constructor(
    id: string,
    label: string,
    body: AnnotationBodyList,
    target: AnnotationSelector,
    motivation: Motivation,
  ) {
    this['@context'] = 'http://www.w3.org/ns/anno.jsonld';
    this.id = id;
    this.label = label;
    this.body = body;
    this.target = target;
    this.motivation = motivation;
  }

  toJSON() {
    return {
      ...this,
      id: this.id === null ? undefined : this.id,
    }
  }

  static fromJsonLD(jsonLd) {
    return new Annotation(
      jsonLd.id,
      jsonLd.label,
      AnnotationBodyList.fromJsonLD(jsonLd.body),
      AnnotationSelector.fromJsonLD(jsonLd.target),
      jsonLd.motivation,
    );
  }

}




