import { HELP_CONTEXT_ADD, HELP_CONTEXT_REMOVE, HELP_INFO_ADD, HELP_INFO_REMOVE } from '../actions/help';
import update from 'immutability-helper';

const defaultState = {
  context: null,
  info: {},
  warning: {},
  error: {},
};

export default function elucidateReducer(state = defaultState, action) {

  switch (action.type) {
    case HELP_INFO_ADD:
      return update(state, {
        [action.payload.type]: {
          [action.payload.key]: { $set: action.payload.content },
        },
      });

    case HELP_INFO_REMOVE:
      return update(state, {
        [action.payload.type]: {
          $apply: function (obj) {
            const { [action.payload.key]: _, ...copy } = obj;
            return copy;
          },
        },
      });

    case HELP_CONTEXT_ADD:
      return update(state, {
        context: { $set: action.payload.content },
      });

    case HELP_CONTEXT_REMOVE:
      return update(state, {
        context: { $set: null },
      });
    default:
      return state;
  }
}



