import OpenSeadragon from 'openseadragon';
import React, {PureComponent} from 'react';
import '../OpenSeadragonViewer.scss';
import ScaledImageViewer from '../ScaledImageViewer';
import {withState} from 'recompose';

class Viewer extends PureComponent {

  asyncAddTile(args) {
    return new Promise((success, err) => {
      try {
        this.viewer.addTiledImage.call(this.viewer, {success, ...args});
      } catch (e) {
        err(e);
      }
    });
  }

  createRelativePoint(x, y) {
    return this.viewer.viewport.viewerElementToImageCoordinates(
      new OpenSeadragon.Point(x, y),
    );
  }

  createViewportPoint(x, y) {
    return this.viewer.viewport.imageToViewerElementCoordinates(
      new OpenSeadragon.Point(x, y),
    );
  }

  getImageSize() {
    return {
      width: this.props.width,
      height: this.props.height,
    }
  }

  componentDidMount() {
    const {getRef, setFallback, onImageLoaded, tileSource} = this.props;
    if (!tileSource) {
      console.error('Something went wrong, we cannot display the open sea dragon');
      setFallback(true);
    }
    this.viewer = new OpenSeadragon({
      element: this.element,
      ajaxWithCredentials: false,
      showNavigator: true,
      showRotationControl: true,
      defaultZoomLevel: 0,
      maxZoomPixelRatio: 2,
      navigatorPosition: 'BOTTOM_RIGHT',
      animationTime: 0.6,
      immediateRender: false,
      preserveViewport: true,
      blendTime: 0,
      minPixelRatio: 0.5,
      visibilityRatio: 0.65,
      constrainDuringPan: false,
      showSequenceControl: false,
      showNavigationControl: false,
      showZoomControl: true,
      showHomeControl: false,
      showFullPageControl: false,
      sequenceMode: true,
      prefixUrl: 'http://zimeon.github.io/iiif-dragndrop/openseadragon/images/',
    });
    if (getRef) {
      getRef(this);
    }

    this.asyncAddTile({tileSource}).then(e => {
      this.viewer.viewport.goHome(true);
      if (onImageLoaded) {
        onImageLoaded(this.viewer, e);
      }
    });
  }

  render() {
    const {height, fallback, width, maxHeight = Infinity, scale} = this.props;

    if (fallback) {
      return (
        <ScaledImageViewer { ...this.props}/>
      );
    }

    const actualHeight = ( height * scale ) < maxHeight ? height * scale : maxHeight;
    return (
      <div style={{position: 'relative'}}>
        <div ref={el => this.element = el} style={{height: actualHeight, width: width * scale}}/>
      </div>
    );
  }
}

export default withState('fallback', 'setFallback', false)(Viewer);



