import { AnnotationSelector } from '../lib/elucidate/index';
export function getServer(state) {
  return state.elucidate.server;
}

export function getAnnotationById(state, id) {
  return state.elucidate.annotations[id];
}

export function isSendingAnnotations(state) {
  return state.elucidate.pendingAnnotations > 0;
}

export function getCanvasFromManifest(manifest, canvasId) {
  if (!(
      manifest ||
      manifest.sequences ||
      manifest.sequences[0] ||
      manifest.sequences[0].canvases
    )) {
    return null;
  }
  const canvases = manifest.sequences[0].canvases;
  for (let canvas of canvases) {
    if (canvas['@id'] === canvasId) {
      return canvas;
    }
  }
  return null;
}

export function getCollectionId(state) {
  return state.elucidate.collection.id;
}

// @todo swap into annotationList eventually.
export function richAnnotationList(state) {
  return annotationList(state).map(annotation => ({
    ...annotation,
    selector: AnnotationSelector.parse(annotation.target),
  }))
}

export function annotationList(state) {
  const elucidateAnnotations = Object.keys(state.elucidate.annotations).map(key => state.elucidate.annotations[key]);
  const elucidateAnnotationsExist = !!elucidateAnnotations.length;
  if (!elucidateAnnotationsExist) {
    return [];
  }
  return elucidateAnnotations;
}



