import React, { Component } from 'react';
import FormField from './FormField';

export default class FormGroup extends Component {

  static DEFAULT_GROUP = 'defaultGroup';

  state = {
    isOpen: false,
  };

  toggle = () => this.setState({ isOpen: !this.state.isOpen });

  render() {
    const { groupKey, style, group, fields, draft, ...props } = this.props;
    const { isOpen } = this.state;
    const groupClass = groupKey === FormGroup.DEFAULT_GROUP || isOpen;
    const groupKeyClass = groupKey.replace(/ /g, '-');

    return (
      <div
        className={style.element('group').modifiers({ [groupKeyClass]: true, open: groupClass })}>
        {groupKey !== FormGroup.DEFAULT_GROUP ? (
          <div onClick={this.toggle}
               className={style.element('group-name').modifiers({ [groupKeyClass]: true, open: isOpen })}>
            {group.label}
          </div>
        ) : null}
        <div className={style.element('group-fields').modifiers({ [groupKeyClass]: true, open: isOpen })}>
          {fields.map(field =>
            <FormField
              key={field.id}
              draftId={draft.id}
              field={field}
              value={draft.input[field.id]}
              preview={draft.isPreviewing}
              {...props}
            />)}
        </div>
      </div>
    );
  }
}



