import BEM from 'digirati-bem-js';
import React, {PureComponent} from 'react';
import './Toggle.scss';

const style = BEM.block('toggle');

class Toggle extends PureComponent {

  render() {
    const {label, isActive, className, onChange} = this.props;
    return (
      <div className={[ style, className ]}>
        <span className={style.element('label')}>{ label }</span>
        <div
          onKeyDown={(e) => {
            if (e.keyCode === 32 || e.keyCode === 13) {
              e.nativeEvent.preventDefault();
              onChange(!isActive);
            }
          }}
          tabIndex="0"
          className={style.element('hitbox').modifier('active', isActive)}
          onClick={() => onChange(!isActive)}>
          <div className={style.element('ball').modifier('active', isActive)}/>
        </div>
      </div>
    );
  }

}

export default Toggle;



