import update from 'immutability-helper';
import {
  ELUCIDATE_ADD_ANNOTATION,
  ELUCIDATE_CREATE_COLLECTION,
  ELUCIDATE_SEND_ANNOTATION,
  ELUCIDATE_SET_COLLECTION,
  ELUCIDATE_SET_RESOURCE,
  ELUCIDATE_SET_SERVER
} from '../actions/elucidate';

const defaultState = {
  server: null,
  resource: null,
  collection: {
    id: null,
    label: null,
    resourceId: null,
    loading: false,
  },
  annotations: {},
  pendingAnnotations: 0,
};

export default function elucidateReducer(state = defaultState, action) {
  switch (action.type) {
    case ELUCIDATE_SET_SERVER:
      return update(state, {
        server: { $set: action.payload.server },
      });

    case ELUCIDATE_SET_RESOURCE:
      return update(state, {
        resource: { $set: action.payload.resource },
      });

    case ELUCIDATE_CREATE_COLLECTION:
      return update(state, {
        collection: {
          loading: { $set: true },
          label: { $set: action.payload.label },
          resourceId: { $set: action.payload.resourceId },
        },
      });

    case ELUCIDATE_SET_COLLECTION:
      return update(state, {
        collection: {
          $set: Object.assign({}, action.payload.collection, { loading: false }),
        },
      });

    case ELUCIDATE_SEND_ANNOTATION:
      return update(state, {
        pendingAnnotations: { $apply: (n) => n + 1 },
      });

    case ELUCIDATE_ADD_ANNOTATION:
      return update(state, {
        pendingAnnotations: { $apply: (n) => n - 1 },
        annotations: {
          [action.payload.id]: {
            $set: action.payload.annotation,
          },
        },
      });

    default:
      return state;
  }
}



