import update from 'immutability-helper';
import {
  MANIFEST_ADD,
  MANIFEST_CANVAS_SELECT,
  MANIFEST_REMOVE,
  MANIFEST_REQUEST,
  MANIFEST_SELECT
} from '../actions/manifest';

const defaultState = {
  currentCanvas: null,
  current: null,
  list: {},
  pending: [],
};

export default function manifestReducer(state = defaultState, action) {

  switch (action.type) {

    case MANIFEST_ADD:
      return update(state, {
        list: {
          $set: {
            [action.payload.url]: action.payload.manifest,
          },
        },
      });

    case MANIFEST_SELECT:
      return update(state, {
        current: {
          $set: action.payload.url,
        },
      });

    case MANIFEST_CANVAS_SELECT:
      return update(state, {
        currentCanvas: {
          $set: action.payload.url,
        },
      });

    case MANIFEST_REMOVE:
      const listUpdate = {
        $apply: function (obj) {
          const { [action.payload.url]: _, ...copy } = obj;
          return copy;
        },
      };
      if (state.current !== action.payload.url) {
        return update(state, {
          list: listUpdate,
        });
      }
      return update(state, {
        current: { $set: null },
        list: listUpdate,
      });

    case MANIFEST_REQUEST:
      return update(state, {
        pending: { $push: [action.payload.url] },
      });
  }

  return state;
}



