import React from 'react';
import {render} from 'react-dom';
import {utils} from 'digirati-annotation-plugin-core';
import TranscriberProvider from './transcriber.provider';
import {importResourceTemplate} from 'digirati-annotation-bridge';

export default function TranscriberPlugin($el, {
  canvas,
  manifest,
  transcriptionVocab,
  serializationStrategy,
  resourceTemplates,
  target = 'canvas',
  ...unknownProps
}, store) {
  const transcription = `${$el.innerText}`;
  utils.unknownPropertyWarning(unknownProps);

  if (resourceTemplates) {
    const {dispatch} = store;

    return fetch(`${resourceTemplates}`)
    .then(r => r.json())
    .then(resourceTemplate => {

      dispatch(importResourceTemplate(resourceTemplate, target));

      return resourceTemplate;
    })
    .then(resourceTemplate => {
      render((
        <TranscriberProvider
          store={store}
          target={target}
          transcription={transcription}
          transcriptionVocab={transcriptionVocab || 'dcterms:description'}
          tree={resourceTemplate['@id']}
          serializationStrategy={serializationStrategy}
          manifest={manifest}
          canvas={canvas}/>
      ), $el);
      return store;
    });
  }
}



