import React from 'react';
import {ConfirmationForm} from 'digirati-annotation-components';

/**
 * Rendering the form, mostly sent off to ConfirmationForm component.
 *
 * @param currentResourceTemplate
 * @param resourceForm
 * @param currentDraft
 * @param previewDraft
 * @param updateDraft
 * @param onSave
 * @param unpreviewDraft
 * @param isSelecting
 * @param isSelectingSecondary
 * @param goBack
 * @param editFieldSelector
 * @param currentTree
 * @param elementEvents
 * @param hideTitle
 * @returns {XML}
 */
export default function renderForm({
  currentResourceTemplate,
  resourceForm,
  currentDraft,
  previewDraft,
  updateDraft,
  onSave,
  unpreviewDraft,
  isSelecting,
  isSelectingSecondary,
  renderResourceEditor,
  importCaptureModel,
  goBack,
  editFieldSelector,
  currentTree,
  elementEvents,
  hideTitle,
  ...props
}) {
  return (
    <div style={{paddingLeft: 20}}>
      { !hideTitle ? (
        <div>
          <h1>{currentResourceTemplate.title}</h1>
          <p>{currentResourceTemplate.description}</p>
        </div>
      ) : null }
      {currentDraft.template === currentTree ? (
        <div>
          <ConfirmationForm
            draft={currentDraft}
            fields={Object.values(resourceForm.fields)}
            isSelecting={isSelectingSecondary || isSelecting}
            displayFieldSelector={editFieldSelector}
            elementEvents={elementEvents}
            saveFieldSelector=""
            onEdit={(draft) => unpreviewDraft(draft.id)}
            onSave={(draft) => onSave(draft)}
            previewDraft={previewDraft}
            importCaptureModel={importCaptureModel}
            renderResourceEditor={renderResourceEditor}
            updateField={updateDraft}
            {...props}
          />
          { goBack ? <a href="#" onClick={(e) => {
            e.preventDefault();
            goBack();
          }}>
            Back
          </a> : null }
        </div>
      ) : (
        <div>Form disabled, please complete other annotation</div>
      )}
    </div>
  );
}



