import { getCurrentResourceTemplate, getForm, getResourceById } from 'digirati-annotation-redux/es/query/resourceQuery';
import { getCurrentDraft } from 'digirati-annotation-redux/es/query/draftQuery';
import { importResource, importResourceTree } from 'digirati-annotation-redux/es/actions/resources';

import {
  createDraftFromAnnotation,
  mapAnnotationBody,
  mapTarget,
  parseTarget,
  resourceTemplateReverseTypeLookup
} from './annotations';

import { motivationFromForm } from './helpers';
import Choice from './model/Choice';
import CaptureModel from './model/CaptureModel';
import { createDraft } from 'digirati-annotation-redux/es/actions/drafts';

/**
 * @deprecated
 */
export function mapDraftToDataSet(fields, values) {
  const contexts = {};
  const body = {};

  fields.forEach(model => {
    if (model.metaData.conformsTo && values[model.id]) {
      contexts[model.metaData.conformsTo.label] = model.metaData.conformsTo.id;
      body[model.metaData.conformsTo.label] = values[model.id];
    }
  });

  return {
    '@context': contexts,
    ...body
  };
}

/**
 * @deprecated
 */
function getLabelFromResourceTemplate(template, data) {
  if (!(
      template.metaData.labelParts &&
      template.metaData.labelParts.label
    )) {
    return null;
  }
  const labelField = template.metaData.labelParts.label;

  return data[labelField] ? data[labelField] : null;
}

/**
 * @deprecated
 */
export function dataSetToAnnotation(template, dataset) {
  const body = {
    type: ['TextualBody', 'Dataset'],
    value: JSON.stringify(dataset),
    format: 'text/plain',
  };
  const label = getLabelFromResourceTemplate(template, dataset);

  return { body, label };
}

export function dataSetToSingleAnnotation(template, dataset) {
  const { metaData: { title, description } } = template;

  dataset.context = dataset.context ? dataset.context : {};
  if (title) {
    dataset.context.Title = 'http://dublincore.org/documents/dcmi-terms/#terms-title';
    dataset.Title = title;
  }
  if (description) {
    dataset.context.Description = 'http://dublincore.org/documents/dcmi-terms/#terms-description';
    dataset.Description = description;
  }

  const body = {
    type: ['TextualBody', 'Dataset'],
    value: JSON.stringify(dataset),
    format: 'text/plain',
  };


  return { body, label: title };
}

/**
 * @deprecated
 */
export function getIndividualW3CAnnotationBodies(state, scope) {
  const currentDraft = getCurrentDraft(state, scope);
  const resourceTemplate = getCurrentResourceTemplate(state, scope);
  const fields = Object.values(getForm(resourceTemplate).fields);

  const selectors = Object.keys(currentDraft.selectors || {})
    .map(key => ({ id: key, value: currentDraft.selectors[key] }))
    .map(selector => {
      const capModel = fields.find(model => model.id === selector.id);
      return {
        id: selector.id,
        annotation: dataSetToSingleAnnotation(
          capModel,
          mapDraftToDataSet(fields, { [selector.id]: currentDraft.input[selector.id] }),
        ),
      }
    });
  const keyedSelectors = {};
  for (let selector of selectors) {
    keyedSelectors[selector.id] = selector.annotation;
  }

  return keyedSelectors;
}

export function getW3CAnnotationBody(state, scope) {
  const currentDraft = getCurrentDraft(state, scope);
  const resourceTemplate = getCurrentResourceTemplate(state, scope);
  const fields = Object.values(getForm(resourceTemplate).fields);

  return dataSetToAnnotation(
    resourceTemplate,
    mapDraftToDataSet(fields, currentDraft.input),
  );
}


export function createDraftFromCaptureModel(scope, captureModelId, draftDefaults = {}, defaultMotivation = 'tagging') {
  return (dispatch, getState) => {
    const state = getState();
    const { resources, motivation, selectors } = getDraftFromCaptureModel(
      getResourceById(state, captureModelId),
    );
    dispatch(
      createDraft(scope, resources, captureModelId, motivation || defaultMotivation, selectors, draftDefaults)
    );
  }
}

export function getDraftFromCaptureModel(template) {
  return {
    motivation: motivationFromForm(
      (template && template.metaData && template.metaData.motivatedBy) ? template.metaData.motivatedBy : {
        id: 'oa:tagging',
        label: 'Tagging',
      },
    ),
    resources: getDraftTemplateFromCaptureModel(template),
    selectors: getDraftSelectorsFromCaptureModel(template),
  }
}

export function getDraftTemplateFromCaptureModel(template) {
  const resources = template.fields;
  return Object.keys(resources).reduce((obj, key) => {
    obj[resources[key].id] = null;
    return obj;
  }, {});
}

export function getDraftSelectorsFromCaptureModel(template) {
  const resources = template.fields;
  return Object.keys(resources).reduce((obj, key) => {
    obj[resources[key].id] = {
      type: resources[key].metaData.selector,
      ...resources[key].metaData.selectorValue
    };
    return obj;
  }, {});
}

export function constructUnknownTemplate(resourceTemplate) {
  if (Choice.is(resourceTemplate)) {
    return Choice.fromJsonLD(resourceTemplate);
  }
  if (CaptureModel.is(resourceTemplate)) {
    return CaptureModel.fromJsonLD(resourceTemplate);
  }
}

export function importResourceTemplate(resourceTemplate, target = 'canvas') {
  return (dispatch) => {
    const tree = constructUnknownTemplate(resourceTemplate);

    if (!tree) {
      return;
    }
    if (tree instanceof CaptureModel) {
      return dispatch(importResource(tree));
    }

    dispatch(importResourceTree(tree.getTree(target)));

    tree
      .flatten()
      .forEach(r => dispatch(importResource(r)));
  }
}


export {
  createDraftFromAnnotation,
  mapAnnotationBody,
  mapTarget,
  parseTarget,
  resourceTemplateReverseTypeLookup,
};



