import React, { PropTypes } from 'react';
import selectorPropTypes from './_selectorPropTypes';
import BoxSelector from './BoxSelector';
import PinSelector from './PinSelector';
import WholeCanvasSelector from './WholeCanvasSelector';

function Selector({ type, ...props }) {

  switch (type) {
    case 'WholeCanvasSelector':
    case 'wholecanvasselector':
      return <WholeCanvasSelector {...props}/>;
    case 'madoc:pin':
      return <PinSelector {...props}/>;
    case 'BoxSelector':
    case 'madoc:boxdraw':
    default:
      return <BoxSelector {...props}/>;
  }
}

Selector.PropTypes = {
  type: PropTypes.string.isRequired,
  ...selectorPropTypes
};

export default Selector;



