import React, { Component } from 'react';
import Draggable from '../../../patches/react-draggable';

export default function withDraggable(WrappedComponent) {

  return class extends Component {

    state = {
      x: 0,
      y: 0,
    };

    onDrag = (e, { deltaX, deltaY }) => this.setState(() => ({
      x: this.state.x + deltaX,
      y: this.state.y + deltaY
    }));

    onStop = () => {
      this.props.onUpdate({
        x: this.props.x + this.state.x,
        y: this.props.y + this.state.y
      });
      this.setState({
        x: 0,
        y: 0
      });
    };

    render() {
      const { canvasScale, drag = false } = this.props;

      if (drag === false) {
        return <WrappedComponent {...this.props}/>;
      }
      return (
        <Draggable
          scale={canvasScale}
          position={{ x: 0, y: 0 }}
          grid={[1, 1]}
          onDrag={ this.onDrag }
          onStop={ this.onStop }
        >
          <div style={{ pointerEvents: 'auto', zIndex: 999999 }}>
            <WrappedComponent {...this.props} onUpdate={null}/>
          </div>
        </Draggable>
      );
    }

  }
}



