import { ANNOTATIONS_ADD } from '../actions/annotations';
import update from 'immutability-helper';

const defaultState = {
  list: [],
};

export default function annotationReducer(state = defaultState, action) {
  if (action.type === ANNOTATIONS_ADD) {
    return update(state, {
      list: { $push: [action.payload.annotation] },
    });
  }
  return state;
}



