import flatten from 'lodash.flatten';

import {
  COMBINE,
  DESCRIPTION,
  EXTERNALISE,
  HAS_PART,
  HUMAN_READABLE,
  ID,
  LABEL_PARTS,
  MOTIVATED_BY,
  MULTIPLE,
  SERIALIZE,
  TITLE,
  UI_CHOICE
} from '../vocab';
import { castBool, get, labelId, motivationFromForm } from '../helpers';
import { constructUnknownTemplate } from '../index';

export default class Choice {

  type = 'choice';

  static is(choice) {
    const $ = get(choice);
    return $(UI_CHOICE) === 'True' && Array.isArray($(HAS_PART))
  }

  isValidTarget(target) {
    return !!this.choice.reduce((s, choice) => {
      if (choice.isValidTarget(target)) {
        s.push(choice);
      }
      return s;
    }, []).length;
  }

  getTree(target) {
    return {
      id: this.id,
      type: this.type,
      title: this.title,
      description: this.description,
      metaData: this.metaData,
      choice: this.choice
        .filter(choice => choice.isValidTarget(target))
        .map(choice => choice.type === 'choice' ? choice.getTree(target) : {
          id: choice.id,
          title: choice.title,
          description: choice.description,
          type: choice.type,
        })
        .reduce((s, a) => {
          s[a.id] = a;
          return s;
        }, {}),
    }
  }

  flatten() {
    const { choice, ...item } = this;
    return flatten([item, ...choice.map(c => c.flatten())])
  }

  static fromJsonLD(jsonLd) {
    const $ = get(jsonLd);
    return new Choice(
      $(ID),
      $(TITLE),
      $(DESCRIPTION),
      {
        combine: castBool($(COMBINE, true)),
        externalise: castBool($(EXTERNALISE, true)),
        humanReadable: castBool($(HUMAN_READABLE, true)),
        serialize: castBool($(SERIALIZE)),
        multiple: castBool($(MULTIPLE)),
        motivatedBy: motivationFromForm(labelId($(MOTIVATED_BY))),
        labelParts: labelId($(LABEL_PARTS)),
      },
      $(HAS_PART).map(
        template => constructUnknownTemplate(template),
      ),
    );
  }

  static fromData({ id, title, description, metaData = {}, choices }) {
    return new Choice(id, title, description, metaData, choices);
  }

  constructor(id, title, description, metaData = {}, choices) {
    this.id = id;
    this.title = title;
    this.description = description;
    this.metaData = metaData;
    this.choice = choices;
  }
}



