import React from 'react';
import {connector} from 'digirati-annotation-redux';
import {currentImageService} from 'digirati-annotation-redux/es/query/manifestQuery';
import { getAnnotationBody } from 'digirati-annotation-bridge/es/annotations';

function getImageFromTarget(state, annotation, target, size) {
  const imageService = currentImageService(state);
  if (imageService) {
    const prefix = imageService.split('info.json')[0];
    const src = `${prefix}${target.x},${target.y},${target.width},${target.height}/${size},/0/default.jpg`;
    const body = getAnnotationBody(annotation);

    return (
      <div className="c-image c-image--sm-sq" key={annotation.id}>
        <div className="c-image__src">
          <img src={src}/>
        </div>
        <div className="c-image__info">
          <p>{body['foaf:name'] ? body['foaf:name'] : body.textualBody ? body.textualBody : <em>No content found</em>}</p>
          <div className="c-image__actions">
          </div>
        </div>
      </div>
    );
  }
}

function RegionAnnotations(props) {
  return (<div className="c-dig-image-grid c-dig-image-grid--sm">
    { props.regionAnnotations ? props.regionAnnotations.map(annotation =>
      getImageFromTarget(props.state, annotation, annotation.selector.target, 150)) :
      'loading...' }
  </div>);
}

export default connector(RegionAnnotations);



