import React, { Component } from 'react';
import StaticImageViewer from './StaticImageViewer';

class ScaledImageViewer extends Component {

  // static propTypes = {
  //     ...viewerPropTypes,
  //     scale: PropTypes.number.isRequired
  // };

  getSelectedRegion() {
    const { regions, scale } = this.props;
    const r = regions.filter(region => region.selected).pop();
    if (!r) return null;
    return {
      height: r.height / scale,
      width: r.width / scale,
      top: r.top / scale,
      left: r.left / scale,
    };
  }

  createRelativePoint(x, y) {
    const { scale } = this.props;
    return {
      x: x / scale, y: y / scale
    };
  }

  createViewportPoint(x, y) {
    const { scale } = this.props;
    return {
      x: x * scale, y: y * scale
    };
  }

  getImageSize() {
    return {
      height: this.props.height,
      width: this.props.width,
    }
  }

  getRef() {
    const {getRef} = this.props;
    if (getRef) {
      getRef(this);
    }
  }

  getRegions() {
    const { regions, scale } = this.props;
    return regions.map(r => ({
      height: r.height / scale,
      width: r.width / scale,
      top: r.top / scale,
      left: r.left / scale,
    }))
  }

  render() {
    const { height, width, top, left, scale, ...props } = this.props;
    return (
      <StaticImageViewer
        height={height * scale}
        width={width * scale}
        top={top * scale}
        left={left * scale}
        modifiers={{ scaled: true }}
        {...props}
      />
    );
  }
}

export default ScaledImageViewer;



