import {
  SELECTOR_CANCEL,
  SELECTOR_CHOOSE,
  SELECTOR_COMMIT_TO_DRAFT,
  SELECTOR_COMMIT_TO_DRAFT_FIELD,
  SELECTOR_SET_AVAILABLE,
  SELECTOR_UPDATE
} from '../actions/selectors';
import update from 'immutability-helper';

const defaultState = {
  available: [],
  currentSelector: {},
};

export default function selectorReducer(state = defaultState, action) {

  switch (action.type) {
    case SELECTOR_SET_AVAILABLE:
      return update(state, {
        available: { $set: action.payload.selectors },
      });

    case SELECTOR_CHOOSE:
      return update(state, {
        currentSelector: {
          $set: {
            type: action.payload.type,
            source: action.payload.source,
            ...action.payload.defaults
          },
        },
      });

    case SELECTOR_UPDATE:
      return update(state, {
        currentSelector: { $merge: action.payload.data },
      });

    case SELECTOR_COMMIT_TO_DRAFT:
    case SELECTOR_COMMIT_TO_DRAFT_FIELD:
    case SELECTOR_CANCEL:
      return update(state, {
        currentSelector: { $set: {} },
      });

  }

  return state;
}



