import React, { PureComponent } from 'react';
import DatePickerComponent from 'react-datepicker';
import moment from 'moment';
import 'react-datepicker/dist/react-datepicker.css';
import BEM from 'digirati-bem-js';
import './TextBox.scss';

const style = BEM.block('input-text-box');

class DatePicker extends PureComponent {

  // propTypes = {
  //   id: PropTypes.string.isRequired,
  //   value: PropTypes.string.isRequired,
  //   update: PropTypes.func.isRequired,
  //   metaData: PropTypes.shape({
  //     title: PropTypes.string.isRequired,
  //     description: PropTypes.string
  //   }).isRequired,
  //   preview: PropTypes.bool,
  // };

  handleChange(time) {
    this.props.update(time.toISOString());
  }

  render() {
    const { id, value, metaData, preview, displayFieldSelector } = this.props;
    const time = moment(value);
    return (
      <div className={style}>
        <label className={style.element('label')} htmlFor={id}>
          {metaData.title} {' '}
          { (metaData.selector && metaData.selectorValue) ?
            (<a onClick={(e) => {
              e.preventDefault();
              displayFieldSelector(id);
            }} href="#">edit</a>)
            : null }
        </label>
        <div className={style.element('description')}>
          {metaData.description}
        </div>
        { preview ? (
          <div className={style.element('preview')}>{value}</div>
        ) : (
          <DatePickerComponent
            dateFormat="DD/MM/YYYY"
            className={style.element('input').modifier('datepicker')}
            selected={time.isValid() ? time : moment()}
            showYearDropdown={true}
            onChange={(e) => this.handleChange(e)}/>
        ) }
      </div>
    );
  }

}

export default DatePicker;



