import BEM from 'digirati-bem-js';
import React, {PureComponent} from 'react';
import './StaticImageView.scss';

const style = BEM.block('static-image-viewer');

class StaticImageViewer extends PureComponent {

  // propTypes = {
  //   ...viewerPropTypes,
  //   modifiers: PropTypes.object
  // };

  getSelectedRegion() {
    const {regions} = this.props;
    const r = regions.filter(region => region.selected).pop();
    if (!r) return null;
    return {
      type: r.type,
      height: r.height,
      width: r.width,
      top: r.top,
      left: r.left,
    };
  }

  getImageSize() {
    return {
      height: this.props.height,
      width: this.props.width,
    }
  }

  createRelativePoint(x, y) {
    return {x, y};
  }

  createViewportPoint(x, y) {
    return {x, y};
  }

  getRef() {
    const {getRef} = this.props;
    if (getRef) {
      getRef(this);
    }
  }

  resolveRegion(r) {
    if (r.unit === 'percent') {
      return this.resolveRegion({
        unit: 'pixel',
        type: r.type,
        left: r.left * this.props.width,
        top: r.top * this.props.height,
        width: r.width * this.props.width,
        height: r.height * this.props.height,
      });
    }

    return {
      type: r.type,
      height: r.height,
      width: r.width,
      top: r.top,
      left: r.left,
    };
  }

  getRegions() {
    return this.props.regions.map(r => this.resolveRegion(r));
  }

  render() {
    const {resource, regions, height, width, modifiers} = this.props;
    return (
      <div className={style.modifiers(modifiers)} style={{height, width}}>
        <div className={style.element('regions')}>
          { regions && regions.map((region, key) => (
            <div
              key={key}
              className={style.element('region-item').modifiers({
                'selected': region.selected,
                'editing': region.editing,
              })}
              style={{
                height: region.height,
                width: region.width,
                top: region.top,
                left: region.left,
              }}
              onClick={ region.onClick ? () => region.onClick(region) : null }>
            </div>
          )) }
        </div>
        <img className={style.element('image')} src={resource} height={height} width={width}/>
      </div>
    );
  }

}

export default StaticImageViewer;



