import BEM from 'digirati-bem-js';
import React, {Component} from 'react';
import './PinSelector.scss';
import withDraggable from '../viewers/openseadragon/withDraggable';
import withFixedScale from '../viewers/openseadragon/withFixedScale';

const $pin = BEM.block('pin-selector');

class PinSelector extends Component {

  render() {
    const { x, y, style } = this.props;
    return (
      x && y ?
        <div className={$pin.element('icon')}
             style={{top: y - 17, left: x - 17, pointerEvents: 'auto', ...style}}>
        </div> : <div />
    );
  }
}

export default withDraggable(
  withFixedScale(
    PinSelector
  )
);



